import React, { useState } from 'react';
import { Form, Input, Button, Row,  Col } from 'antd';
import { Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';

import { register } from '../../../api';
import { AlertError } from '../../alerts/alerts';
import { SelectDate, SelectMonth, SelectYear, yearNow } from '../../form/select';

import {
    loadingStart,
    loadingClose,
    loadingContent,
    loadingSuccess
} from '../../../redux/loadingmodal/actionCreator';

const RegisterForm = (props) => {

    const dispatch = useDispatch();
    const [alert, setAlert] = useState('');

    const [form] = Form.useForm();

    const submitForm = async (e) => {

        if(e.birth_date_y > yearNow()) {
            return form.setFields([ { name: 'birth_date_y', errors: ['Tahun tidak valid'] } ]);
        }

        setAlert('');
        dispatch(loadingStart());
        dispatch(loadingContent('Memproses pendaftaran...'));

        let _to = setTimeout(() => dispatch(loadingContent('sedang memproses...')), 5000);

        const {error, message, errors} = await register(e);

        clearTimeout(_to);

        if(error) {
            dispatch(loadingClose());
            setAlert(<AlertError message={message} />);
            form.setFields(errors);
        } else {
            dispatch(loadingSuccess());
            dispatch(loadingContent('Pendaftaran berhasil! link verifikasi akan dikirim melalui email/nomor whatsapp anda...'));
            setTimeout(() => window.location = '/auth', 5000);
        }

    }

    return(
        <Row  gutter={25} className="text-left">
                <Col lg={24} xs={24}>
                    {alert}
                    <Form layout="vertical" form={form} onFinish={submitForm}>
                        <Row gutter={25}>
                            <Col lg={12} xs={24}>
                                <Form.Item name="first_name" label="Nama Depan"
                                    rules={[{required: true, message: 'Masukan nama depan'}]}
                                >
                                    <Input placeholder="..." autoFocus />
                                </Form.Item>
                            </Col>
                            <Col lg={12} xs={24}>
                                <Form.Item name="last_name" label="Nama Belakang">
                                    <Input placeholder="..." autoFocus />
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row gutter={25}>
                            <Col lg={24} xs={24}>
                                <Form.Item name="contact" label="Alamat Email/No Telepon"
                                    rules={[{required: true, message: 'Masukan email/nomor whatsapp'}]}
                                >
                                    <Input placeholder="..." autoFocus />
                                </Form.Item>
                            </Col>
                        </Row>
                        
                        <Row gutter={25}>
                            <Col lg={24} xs={24}>
                                <Form.Item name="password" label="Password"
                                    rules={[{required: true, message: 'Masukan password'}]}
                                >
                                    <Input.Password placeholder="Enter Password" autoComplete="false"/>
                                </Form.Item>
                            </Col>
                        </Row>

                        <Row gutter={25}>
                            <Col lg={8} xs={24}>
                                <Form.Item name="birth_date_d" label="Tanggal Lahir"
                                    rules={[{required: true, message: 'Pilih tanggal lahir'}]}
                                >
                                    <SelectDate/>
                                </Form.Item>
                            </Col>
                            <Col lg={8} xs={24}>
                                <Form.Item name="birth_date_m" label={(<br/>)} className="no-required"
                                    rules={[{required: true, 'message': 'Pilih bulan lahir'}]}
                                >
                                    <SelectMonth/>
                                </Form.Item>
                            </Col>
                            <Col lg={8} xs={24}>
                                <Form.Item name="birth_date_y" label={(<br/>)} className="no-required"
                                    rules={[{required: true, 'message': 'Pilih tahun lahir'}]}
                                >
                                    <SelectYear/>
                                </Form.Item>
                            </Col>

                            <Col lg={24}  xs={24}>
                                <small>
                                Dengan mengklik Daftar, Anda menyetujui Ketentuan, Kebijakan Data kami.
                                Anda akan menerima Notifikasi Email/Sms dari kami dan dapat menolaknya kapan saja.
                                </small> <br/> <br/>
                            </Col>
                        </Row>
                        <Form.Item>
                            <Button type="primary" size="large" htmlType="submit" className="login-form-button" block={true}>
                            Daftar
                            </Button>
                        </Form.Item>
                    </Form>
                </Col>
            </Row>
        );
}

export default RegisterForm;