const registerStorageKey = 'registerObj';

const getItem = key => {
  const data = typeof window !== 'undefined' ? localStorage.getItem(key) : '';

  try {
    return JSON.parse(data);
  } catch (err) {
    return data;
  }
};

const setItem = (key, value) => {
  const stringify = typeof value !== 'string' ? JSON.stringify(value) : value;
  return localStorage.setItem(key, stringify);
};

const removeItem = key => {
  localStorage.removeItem(key);
};

export const registerStorage = () => {
  let storageRegister = JSON.parse(localStorage.getItem(registerStorageKey));
  
  if(storageRegister === null) {
    storageRegister = {};
    localStorage.setItem(registerStorageKey, JSON.stringify({}));
  }

  return storageRegister;
}

export const registerStorageAddItem = (key, value) => {
  let storage = registerStorage();
      storage[key] = value;

  localStorage.setItem(registerStorageKey, JSON.stringify(storage));
}

export const registerStorageItem = (key) => {
  let storage = registerStorage();
  return storage[key];
}

export const registerStorageRemoveItem = (key) => {
  let storage = registerStorage();

  delete storage[key];

  localStorage.setItem(registerStorageKey, JSON.stringify(storage));
}

export const registerStorageClear = () => {
  localStorage.setItem(registerStorageKey, JSON.stringify({}));
}

export { getItem, setItem, removeItem };
