import React from 'react';
import { Switch, Route } from 'react-router-dom';
import { Row, Col } from 'antd';
import { Cards } from '../../components/cards/frame/cards-frame';
import { Main } from '../styled';
import { MenuList } from '../../components/menu';

import DashboardPage from '../dashboard';
import FamilyMember from '../family/list';
import FamilyMemberDetail from '../family/detail';
import FormPersonalInformation from '../../components/form/form/personalinformation';
import AppointmentRegisterScheduleList from '../appointment/schedule';
import AppointmentRegisterChooseSchedule from '../appointment/choose_schedule';
import AppointmentRegisterFormInformation from '../appointment/form_information';
import AppointmentRegisterConfirmation from '../appointment/confirmation';
import AppointmentHistory from '../appointment/history';
import AppointmentDetail from '../appointment/detail';
import Profile from '../profile/index';
import NewsList from '../news/list';
import NewsDetail from '../news/detail';

import BillList from '../bill/list';
import BillDetail from '../bill/detail';

const NotFound = () => {
    return(
        <div>
            404 Not Found
        </div>
    );
}

const Dashboard = () => {

  return (
    <>
      <Main>
        <Row gutter={25}>

          <Col lg={24} xs={24} style={{ marginBottom: '10px' }}>
            <MenuList/>
          </Col>

          <Col lg={24} xs={24}>
            <Cards headless>
              <div style={{ minHeight: 'calc(100vh - 320px)', display: 'flex', flexDirection: 'column' }}>
                <Switch>
                    <Route exact path='/home' component={DashboardPage} />

                    <Route exact path='/appointment/schedule' component={AppointmentRegisterScheduleList} />
                    <Route exact path='/appointment/register/schedule' component={AppointmentRegisterChooseSchedule} />
                    <Route exact path='/appointment/register/data' component={AppointmentRegisterFormInformation} />
                    <Route exact path='/appointment/register/confirmation' component={AppointmentRegisterConfirmation} />
                    <Route exact path='/appointment_history' component={AppointmentHistory} />
                    <Route exact path='/appointment_history/:id' component={AppointmentDetail} />

                    <Route exact path='/family/member' component={FamilyMember} />
                    <Route path='/family/member/add' component={() =>  {
                      return <FormPersonalInformation new={true}/>
                    }} />
                    <Route path='/family/member/detail/:id' component={FamilyMemberDetail} />
                    <Route path='/family/member/update/:id' component={FormPersonalInformation} />
                    <Route path='/profile' component={Profile} />
                    <Route exact path='/news' component={NewsList} />
                    <Route exact path='/news/:id' component={NewsDetail} />

                    <Route exact path='/bills' component={BillList} />
                    <Route exact path='/bills/:id' component={BillDetail} />

                    <Route component={NotFound} />
                </Switch>
              </div>
            </Cards>
          </Col>
        </Row>
      </Main>
    </>
  );
};

export default Dashboard;
