import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router';
import Webcam from "react-webcam";
import { Form, Row, Col, Input, Upload, Avatar, PageHeader } from 'antd';
import Cropper from "react-cropper";
import "cropperjs/dist/cropper.css";

import { BasicFormWrapper, } from '../../styled';
import { ContentSection } from '../../../container/styled';
import {
    SelectTitle,
    SelectIdentityType,
    SelectLivingArea,
    SelectReligion,
    SelectMarriedStatus,
    SelectDate,
    SelectMonth,
    SelectYear
} from '../select';
import { RadioGender } from '../radio';

import { Button } from '../../buttons/buttons';
import { Modal } from '../../modals/antd-modals';
import { AlertError } from '../../alerts/alerts';
import { update_profile, add_family } from '../../../api';

import { useDispatch } from 'react-redux';
import Loading from '../../loadings';

const PersonalInformation = (props) => {

    const {
        match: {
            params: {id=null} = {},
        } = {},
    } = props;

    const dispatch = useDispatch();
    const history = useHistory();

    const [form] = Form.useForm();
    const [image, setImage] = useState(null);
    const [cropData, setCropData] = useState(null);
    const [cropper, setCropper] = useState();
    const [modalDisable, setModalDisable] = useState(false);
    const [cropModal, setCropModal] = useState({ visible: false, disable: false });
    const [loadWebcam, setLoadWebcam] = useState(false);
    const [webcamModalInv, setWebcamModalInv] = useState(false);
    const [message, setMessage] = useState(null);
    const [alert, setAlert] = useState(null);
    const [statePersonData, setStatePersonData] = useState({});
    const [loading, setLoading] = useState(false);

    const [loadingStatus, setLoadingStatus] = useState(null);
    const [loadingVisible, setLoadingVisible] = useState(false);
    const [loadingContent, setLoadingContent] = useState(null);

    const titleChange = (val) => {
        if(val == '3' || val == '5') {
            form.setFieldsValue({ gender: 1 });
        } else if (val == '4' || val == '6') {
            form.setFieldsValue({ gender: 2 });
        } else {
            form.setFieldsValue({ gender: 0 });
        }
    }

    const selectProfilePic = (e) => {
        let url = URL.createObjectURL(e);
        setImage(url);
        setCropModal({...cropModal, visible: true, title: 'Potong Gambar'});
        return false;
    }

    const getCropData = () => {
        if (typeof cropper !== "undefined") {
            setModalDisable(true);
            const crop = async () => {
                let imageCropped = cropper.getCroppedCanvas({ height: 320, width: 320, }).toDataURL();
                setCropData(imageCropped);
                setModalDisable(false);
                setCropModal({
                    ...cropModal,
                    visible: false,
                    title: 'Potong Gambar',
                })
                setImage("#");
            }

            setTimeout(crop, 1000); 
        }
      };


    const webcamRef = React.useRef(null);

    const modalWebcam = (type='open') => {
        if(type === 'open') {
            setWebcamModalInv(true);
            setLoadWebcam(true);
        } else {
            setLoadWebcam(false);
            setTimeout(() => setWebcamModalInv(false), 100);
        }
    }

    const captureWebcam = () => {
        const imageSrc = webcamRef.current.getScreenshot();
        setCropData(imageSrc);
        modalWebcam('close');
        return false;
    }

    useEffect(() => {
        form.setFieldsValue({
            profile_pic: cropData
        });
    }, [cropData]);

    useEffect(() => {
        let formPersonData = localStorage.getItem('formPersonData');
        let personData = {};

        if(!props.new) {
            if(formPersonData) {
                personData = formPersonData;
            } else {
                let familyData = localStorage.getItem('family_profile');
                if(familyData) personData = familyData;
                else personData = localStorage.getItem('personData');
            }
        } else {
            personData = formPersonData;//JSON.stringify({});
        }

        personData = JSON.parse(personData);

        if(!personData) return;

        if( Object.keys(personData).length > 0 ) {
            let birth_date_part = personData?.birth_date ? personData?.birth_date.split('-') : (
                !personData?.birth_date_d ? [1990,1,1] : [
                    personData.birth_date_y, personData.birth_date_m, personData.birth_date_d
                ]
            );
            personData.birth_date_d = birth_date_part[2];
            personData.birth_date_m = birth_date_part[1];
            personData.birth_date_y = birth_date_part[0];
            if(personData.profile_pic && id) {
                setCropData(personData.profile_pic);
            }
            form.setFieldsValue(personData);
            setStatePersonData(personData);
            localStorage.setItem('formPersonData', JSON.stringify(personData));
        }
    }, []);

    const formValueChange = (e) => {
        let key = Object.keys(e)[0];
        statePersonData[key] = e[key];
        setStatePersonData(statePersonData);
        
        localStorage.setItem('formPersonData', JSON.stringify(statePersonData));
    }

    const submitForm = async (fields) => {
        setLoadingVisible(true);
        setLoadingStatus('loading');
        setLoadingContent('memproses permintaan...');
        
        let res;
        if(props?.new) {
            res = await add_family(fields);
        } else {
            res = await update_profile(id, fields);
        }
        const {result, error, message, errors, forceStop} = res;
        if(forceStop) return;

        if(error) {
            setLoadingVisible(false);
            form.setFields(errors);
            setAlert(<AlertError message={message} />);
        } else {
            localStorage.removeItem('formPersonData');
            localStorage.removeItem('family_profile');
            setLoadingStatus('ok');
            setLoadingContent('Berhasil menambah keluarga! anda akan dialihkan...');
            setTimeout(() => {
                setLoadingVisible(false);
                history.push('/family/member/detail/' + result.person_id)
            }, 3000);
        }
    }

    return(<ContentSection>
        <PageHeader
            ghost={false}
            onBack={() => window.history.back()}
            title={ (id ? "Update " : "Tambah ") + "Data Anggota Keluarga"}
        />
        {alert}
        <BasicFormWrapper>
            <Form
                form={form}
                onFinish={submitForm}
                layout="vertical"
                onValuesChange={formValueChange}
                scrollToFirstError={true}
            >
                <Row gutter={25}>
                    <Col lg={24} xs={24}>
                        <Row gutter={25}>
                            <Col lg={24} xs={24} className="text-center">
                                <Form.Item name="profile_pic" label="Foto Terbaru" rules={[{required: true, message: 'Pilih Foto Terbaru'}]} >
                                    <Input type="hidden" />
                                </Form.Item>
                                { !cropData ? null :
                                    <>
                                    <Avatar
                                        size={{ xs: 100, sm: 100, md: 100, lg: 100, xl: 100, xxl: 100 }}
                                        src={cropData}
                                    /> <br/><br/>
                                    </>
                                }
                                <Row gutter={25}>
                                    <Col lg={24} xs={24} style={{margin: 'auto'}}>
                                        <Upload accept="image/*" beforeUpload={selectProfilePic} showUploadList={false}>
                                            <Button type="primary" size="small">Pilih Dari File</Button>
                                        </Upload> &nbsp;&nbsp;
                                        <Button type="primary" size="small" onClick={() => modalWebcam('open')}>Ambil Gambar</Button>
                                    </Col>
                                </Row>
                            </Col>
                        </Row> <br/>

                        <Row gutter={25}>
                            <Col lg={12} xs={24}>
                                <Form.Item name="title_id" label="Titel" rules={[{required: true, message: 'Pilih titel'}]}>
                                    <SelectTitle onChange={titleChange}/>
                                </Form.Item>
                            </Col>
                            <Col lg={12} xs={24}>
                                <Form.Item name="gender_id" label="Jenis Kelamin">
                                    <RadioGender buttonStyle="solid"/>
                                </Form.Item>
                            </Col>
                        </Row> <br/>

                        <Row gutter={25}>
                            <Col lg={12} xs={24}>
                                <Form.Item name="first_name" label="Nama Depan" rules={[{required: true, message: 'Masukan nama depan'}]}>
                                    <Input placeholder="..." />
                                </Form.Item>
                            </Col>
                            <Col lg={12} xs={24}>
                                <Form.Item name="last_name" label="Nama Belakang">
                                    <Input placeholder="..." />
                                </Form.Item>
                            </Col>
                        </Row> <br/>

                        <Row gutter={25}>
                            <Col lg={4} xs={24}>
                                <Form.Item name="identity_type_id" label="Tipe Identitas">
                                    <SelectIdentityType placeholder="Pilih tipe identitas"/>
                                </Form.Item>
                            </Col>
                            <Col lg={8} xs={24}>
                                <Form.Item name="identity_number" label="Nomor Identitas" rules={[{required: true, message: 'Masukan nomor identitas'}]}>
                                    <Input placeholder="..." />
                                </Form.Item>
                            </Col>
                            { !id && !props.new && (<Col lg={6} xs={24}>
                                    <Form.Item name="email" label="Alamat Email" rules={[{required: true, message: 'Masukan alamat email'}]}>
                                        <Input placeholder="..." type="email"/>
                                    </Form.Item>
                                </Col>)
                            }
                            <Col lg={(!id && !props.new ? 6 : 12)} xs={24}>
                                <Form.Item name="phone_number" label="Nomor Telepon" rules={[{required: true, message: 'Masukan nomor telepon'}]}>
                                    <Input placeholder="..."/>
                                </Form.Item>
                            </Col>
                        </Row> <br/>

                        <Row gutter={25}>
                            <Col lg={12} xs={24}>
                                <Form.Item name="birth_place" label="Tempat Lahir" rules={[{required: true, message: 'Masukan tempat lahir'}]}>
                                    <Input placeholder="..." />
                                </Form.Item>
                            </Col>
                            <Col lg={12} xs={24}>
                                <Row gutter={25}>
                                    <Col lg={8} xs={24}>
                                        <Form.Item name="birth_date_d" label="Tanggal Lahir"
                                            rules={[{required: true, message: 'Pilih tanggal lahir'}]}
                                        >
                                            <SelectDate/>
                                        </Form.Item>
                                    </Col>
                                    <Col lg={8} xs={24}>
                                        <Form.Item name="birth_date_m" label={(<br/>)} className="no-required"
                                            rules={[{required: true, 'message': 'Pilih bulan lahir'}]}
                                        >
                                            <SelectMonth/>
                                        </Form.Item>
                                    </Col>
                                    <Col lg={8} xs={24}>
                                        <Form.Item name="birth_date_y" label={(<br/>)} className="no-required"
                                            rules={[{required: true, 'message': 'Pilih tahun lahir'}]}
                                        >
                                            <SelectYear/>
                                        </Form.Item>
                                    </Col>
                                </Row>
                                {/* <DatePickerWrapper>
                                    <Form.Item name="birth_date" label="Tanggal Lahir" rules={[{required: true, message: 'Masukan tanggal lahir'}]}>
                                            <DatePicker />
                                    </Form.Item>
                                </DatePickerWrapper> */}
                            </Col>
                        </Row> <br/>

                        <Row gutter={25}>
                            <Col lg={12} xs={24}>
                                <Form.Item name="married_status_id" label="Status Pernikahan" rules={[{required: true, message: 'Pilih status pernikahan'}]}>
                                    <SelectMarriedStatus/>
                                </Form.Item>
                            </Col>
                            <Col lg={12} xs={24}>
                                <Form.Item name="religion_id" label="Agama" rules={[{required: true, message: 'Pilih agama'}]}>
                                    <SelectReligion/>
                                </Form.Item>
                            </Col>
                        </Row> <br/>

                        <Row gutter={25}>
                            <Col lg={24} xs={24}>
                                <Form.Item name="village_id" label="Wilayah Tinggal" rules={[{required: true, message: 'Pilih wilayah tinggal'}]}>
                                    <SelectLivingArea/>
                                </Form.Item>
                            </Col>
                        </Row> <br/>

                        <Row gutter={25}>
                            <Col lg={24} xs={24}>
                                <Form.Item name="address" label="Alamat Tinggal" rules={[{required: true, message: 'Masukan alamat lengkap'}]}>
                                    <Input.TextArea rows={5} placeholder="..." />
                                </Form.Item>
                            </Col>
                        </Row> <br/>

                        <Row gutter={25}>
                            <Col lg={24} xs={24}>
                                <Form.Item>
                                    <Button type="primary" htmlType="submit" className="login-form-button" block={true}>
                                        Submit Data
                                    </Button>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Col>
                </Row>
            </Form>
        </BasicFormWrapper>
        <Modal
            visible={cropModal.visible}
            type="primary"
            title="Potong Gambar"
            onConfirm={getCropData}
            btnConfirmText={ modalDisable ? "proses..." : "Potong Gambar" }
            disableButton={modalDisable}
            onCancel={() => setCropModal({...cropModal, visible: false})}
        >
            <Cropper
                style={{ height: 400, width: "100%" }}
                aspectRatio={1}
                src={image}
                viewMode={1}
                guides={true}
                background={false}
                responsive={true}
                autoCropArea={1}
                checkOrientation={false} // https://github.com/fengyuanchen/cropperjs/issues/671
                onInitialized={(instance) => {
                    setCropper(instance);
                }}
                />
        </Modal>

        <Modal
            visible={webcamModalInv}
            type="primary"
            title="Ambil Gambar"
            onConfirm={captureWebcam}
            btnConfirmText={ modalDisable ? "proses..." : "Ambil Gambar" }
            disableButton={modalDisable}
            onCancel={() => modalWebcam('close')}
        >
            <center>
            { !loadWebcam ? null : 
                <Webcam
                    audio={false}
                    height={320}
                    width={320}
                    imageSmoothing={true}
                    ref={webcamRef}
                    screenshotFormat="image/jpeg"
                />
            }
            </center>
        </Modal>
    
        <Modal
            visible={loadingVisible}
            centered={true}
            footer={null}
            closable={false}
            width={null}
            maskClosable={false}
            zIndex={99999}
        >
            <div className="text-center">
                <Loading status={loadingStatus}/>
                {loadingContent}
            </div>
        </Modal>
    </ContentSection>);
}

export default PersonalInformation;