import React from 'react';
import Swal from 'sweetalert2';
import queryString from 'query-string';
import { withRouter } from "react-router-dom";
import { PageHeader, Row, Col, Select, Image, Form, Empty, Radio, Skeleton } from 'antd';

import { ContentSection, RadioWrapperPad } from '../styled';
import Loading from '../../components/loadings';
import { get_schedule_date, get_schedule_time } from '../../api';
import { Button } from '../../components/buttons/buttons';

import { registerStorage, registerStorageAddItem, registerStorageClear, registerStorageItem } from '../../utility/localStorageControl';
import { format_rupiah } from '../../utility/utility';

class Register extends React.Component {

    params = queryString.parse(window.location.search);
    formRef = React.createRef();
    storageKey = 'registerObj';

    state = {
        arr_consul_date: {},
        arr_consul_time: {},
        consul_date: null,
        consul_time: null,
        schedule_id: this.params.schedule_id,
        data: {},
        loadingTime: false,
        timeError: false,
        loading: true,
    }

    async getData() {
        this.setState({...this.state, loading: true});
        const {result, error, message} = await get_schedule_date(this.state.schedule_id);
        this.setState({...this.state, loading: false});
        if(error) {
            console.log('error:', message);
        } else {
            this.setState({
                ...this.state, data: result.data, arr_consul_date: result.data.date
            }, () => {
                registerStorageAddItem('scheduleObj', JSON.stringify(this.state.data));
                let log_consul_date = registerStorageItem('consul_date');
                    if(log_consul_date) {
                        this.formRef.current.setFieldsValue({
                            consul_date: log_consul_date
                        });
                        this.setState({
                            ...this.state, consul_date: log_consul_date, loadingTime: true
                        },() => {
                            this.getTime();
                        });
                    }
            });
        }
    }

    async getTime()  {
        this.setState({
            ...this.state,
            loadingTime: true,
            timeError: false
        });

        const {result, error, message} = await get_schedule_time(this.state.schedule_id, {
            date: this.state.consul_date
        });

        if(error) {
            this.setState({
                ...this.state,
                loadingTime: false,
                timeError: message
            });
        } else {

            this.setState({
                ...this.state,
                loadingTime: false,
                arr_consul_time: result.data
            }, () => {
                let log_consul_time = registerStorageItem('consul_time');
                this.formRef.current.setFieldsValue({
                    consul_time: log_consul_time
                });
            });
        }
    }

    componentDidMount() {

        let storage = registerStorage();

        if(!this.state.schedule_id) {
            if(!storage?.schedule_id) {
                Swal.fire({
                    icon: 'error',
                    text: 'Terjadi kesalahan, silahkan ulangi proses dari awal'
                }).then(result => this.props?.history.push('/schedule'));
            } else {
                this.setState({
                    ...this.state, schedule_id: storage.schedule_id
                },() => {
                    registerStorageAddItem('schedule_id', this.state.schedule_id);
                    this.getData();
                });
            }
            return;
        };

        if(storage?.schedule_id != this.state.schedule_id) registerStorageClear();
        registerStorageAddItem('schedule_id', this.state.schedule_id);

        this.getData();
    }

    fieldsChangeHandler(e) {
        let key = Object.keys(e)[0];
        registerStorageAddItem(key, e[key]);
        if(key === 'consul_date') {
            this.setState({
                ...this.state, consul_date: e[key], loadingTime: true
            },() => {
                this.getTime();
            });
        }
    }

    formSubmit (e) {
        return this.props?.history.push('/appointment/register/data');
    }

    render() {

        const AppointmentTime = (props) => {
            return(this.state.timeError ? <span className="color-error">{this.state.timeError}</span> : <Radio.Group buttonStyle="solid" {...props}>
            {   this.state.arr_consul_time.map(row => {
                    return <Radio.Button
                                key={this.state.consul_date + '_' + row.time}
                                value={row.time}
                                disabled={(row.status !== 'available' ? true : false )}
                                className={(
                                    row.status === 'available' ? '' :
                                    row.status === 'not_available' ? '' :
                                    row.status === 'waiting_payment' ? 'schedule_time-used' : ''
                                )}
                            >{row.time}</Radio.Button>;
                })
            }
        </Radio.Group>);
        }

        return(
            <ContentSection>
                <PageHeader
                    ghost={false}
                    onBack={() => window.history.back()}
                    title="Langkah 2/4: Pilih tanggal telekonsultasi"
                /> <br/> <br/>
                <Row>
                    <Col lg={12} sm={24} xs={24} className="text-center">
                        { this.state.loading ? <Skeleton.Image/>:
                            <Image
                                src={this.state.data.doctor_pic}
                                preview={false}
                            />
                        }
                    </Col>
                    <Col lg={12} sm={24} xs={24}>
                        { this.state.loading ? <Skeleton/> :
                            <>
                                <div className="text-center">
                                    <h2>{this.state.data.doctor}</h2>
                                    <h4 className="color-primary">{this.state.data.department} | {this.state.data.branch}</h4>
                                    <b>Tarif Telekonsultasi <span style={{color: 'red'}}>Rp {format_rupiah(this.state.data.fee)}</span></b>
                                </div> <br/>
                                <Form
                                    layout="vertical"
                                    onValuesChange={this.fieldsChangeHandler.bind(this)}
                                    onFinish={this.formSubmit.bind(this)}
                                    ref={this.formRef}
                                >
                                    <Form.Item
                                        label="Tanggal Telekonsultasi"
                                        rules={[{required: true, message: 'Pilih tanggal telekonsultasi'}]}
                                        name="consul_date"
                                    >
                                        <Select
                                            placeholder="Pilih tanggal telekonsultasi"
                                        >
                                            {   Object.keys(this.state.arr_consul_date).map(idx => {
                                                    let row = this.state.arr_consul_date[idx];
                                                    return <Select.Option key={row.date} value={row.date}>{row.translate}</Select.Option>;
                                                })
                                            }
                                        </Select>
                                    </Form.Item> <br/>
                                    
                                    <RadioWrapperPad>
                                    <Form.Item
                                        label="Jam Telekonsultasi"
                                        rules={[{required: true, message: 'Pilih jam konsultasi'}]}
                                        name="consul_time"
                                    >
                                        { !this.state.consul_date ?
                                            <Empty description="Pilih tanggal terlebih dahulu untuk melihat jam konsultasi" /> :
                                            this.state.loadingTime ?
                                            <div  className="text-center">
                                                <Loading/> <br/>
                                                Mengambil jam konsultasi...
                                            </div>
                                            : <AppointmentTime/> }
                                    </Form.Item>
                                    
                                    </RadioWrapperPad>
                                    <br/> <br/>

                                    <Form.Item className="text-center">
                                        <Button type="primary" size="large" htmlType="submit" block={true}>
                                            Selanjutnya &nbsp; <i className="fa fa-arrow-circle-right" aria-hidden={true}></i>
                                        </Button>
                                    </Form.Item>
                                </Form>
                            </>
                        }
                    </Col>
                </Row>
            </ContentSection>
        );
    }
}

export default withRouter (Register);