import React from 'react';
import { Radio } from 'antd';

import { genderConfig } from '../config';


export const RadioGender = (props) => {

    return (
        <Radio.Group {...props}>
            {   Object.keys(genderConfig).map(index => {
                    let data = genderConfig[index]
                    return <Radio.Button key={index} value={parseInt(index)}>{data.name}</Radio.Button>
                })
            }
        </Radio.Group>
    );
}