import React, { useEffect, useState } from 'react';
import { Row, Col, Card, Skeleton, Empty } from 'antd';
import { Link } from 'react-router-dom';

import { GalleryCard, ListUserWrapper } from '../styled';
import Heading from '../../components/heading/heading';
import { label_apstatus } from '../../utility/utility';

import { dashboard_data } from '../../api';

import FormPersonalInformation from '../../components/form/form/personalinformation';

const Dashboard = () => {

  const [loading, setLoading] = useState(true);
  const [layout, setLayout] = useState('dashboard');
  const [user, setUser] = useState({});
  const [data, setData] = useState({});

  const getData = async () => {
    setLoading(true);
    const {result, error, message, errors, forceStop} = await dashboard_data();
    if(forceStop) return;

    setLoading(false);

    if(!error) {
      setData(result.data);
    }
  }

  useEffect(() => {
    let storage = localStorage.getItem('personData');
    let objUser = JSON.parse(storage);
    if(objUser?.complete_info) {
      setUser(JSON.parse(storage));
      getData();
    } else {
      setLayout('personal_information_form');
      setLoading(false);
    }
  }, []);

  const DashboardLayout = () => {
    return(
        <>
            <h2><b>Dashboard Pasien</b></h2>
            <Row gutter={[25, 25]}>
              <Col
                lg={{ span: 8, order: 1 }}
                xs={{ span: 24, order: 3 }}
              >
                  <ListUserWrapper>
                    <Card
                      title="Anggota Keluarga"
                    >
                      <div className="project-users">
                        {loading ? <Skeleton avatar/> : 
                        !data?.family ? <Empty/> : data.family.map(row => {
                            return <Link to={"/family/member/detail/" + row.person_id } key={row.person_id} className="porject-user-single">
                            <div>
                              <img src={row.profile_pic} alt="" />
                            </div>
                            <div>
                              <Heading as="h5">{row.full_name}</Heading>
                              <p>{row.age}</p>
                            </div>
                          </Link>
                          })
                        }
                      </div>
                    </Card>
                  </ListUserWrapper>
              </Col>

              <Col
                lg={{ span: 8, order: 2 }}
                xs={{ span: 24, order: 2 }}
              >
                  <ListUserWrapper>
                    <Card
                      title="Daftar Perjanjian"
                    >
                      <div className="project-users">
                        
                        {loading ? <Skeleton avatar/> :
                        !data?.appointment ? <Empty/> : data.appointment.map(row => {
                            return <Link to={"/appointment_history/" + row.appointment_id} key={row.appointment_id} className="porject-user-single">
                            <div>
                              <img src={row.doctor_pic} alt="" />
                            </div>
                            <div>
                              <Heading as="h5">{row.doctor_name}</Heading>
                              <p>
                                <span className="color-error">{row.id_consul_date} {row.consul_time}</span> <br/>
                                {label_apstatus(row.status)}
                              </p>
                            </div>
                          </Link>
                          })
                        }
                      </div>
                    </Card>
                  </ListUserWrapper>
              </Col>
              <Col
                lg={{ span: 8, order: 3 }}
                xs={{ span: 24, order: 1 }}
              >
              <h2>Berita Terbaru</h2>
                
                {loading ? <Skeleton/> :
                !data?.news ? <Empty/> : data.news.map(row => {
                    return <Link to={"/news/" + row.news_id} key={"news_" + row.news_id}>
                    <GalleryCard style={{ marginBottom: '25px' }}>
                        <figure>
                            <img style={{ width: '100%' }} src={row.thumbnail} alt="" />
                            <figcaption>
                                <b className="color-dark"> <br/>
                                {row.title}
                                </b>
                            </figcaption>
                        </figure>
                    </GalleryCard>
                </Link>
                  })
                }
              </Col>
            </Row>
        </>
    );
  }

  return (
    layout === 'personal_information_form' ?  <FormPersonalInformation/> :  <DashboardLayout/>
  );
};

export default Dashboard;
