import Cookies from 'js-cookie';
import actions from './actions';

const { loginBegin, loginSuccess, loginErr, logoutBegin, logoutSuccess, logoutErr, loginModal, modalProps } = actions;

const login = () => {
  return async dispatch => {
    try {
      dispatch(loginBegin());
      setTimeout(() => {
        Cookies.set('logedIn', true);
        return dispatch(loginSuccess(true));
      }, 1000);
    } catch (err) {
      dispatch(loginErr(err));
    }
  };
};

const logOut = () => {
  return async dispatch => {
    try {
      dispatch(logoutBegin());
      Cookies.remove('token');
      dispatch(logoutSuccess(null));
    } catch (err) {
      dispatch(logoutErr(err));
    }
  };
};

const showLoginModal = (type) => {
  return async dispatch => {
    dispatch(loginModal(type))
  }
}

const loginModalProps = (data) => {
  return async dispatch => {
    dispatch(modalProps(data));
  }
}

export {
  loginModalProps,
  login,
  logOut,
  showLoginModal,
  loginModal,
  loginSuccess
};
