import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { Row, Col, Descriptions, Avatar, PageHeader, Skeleton  } from 'antd';
import { Link } from 'react-router-dom';
import { ContentSection } from '../styled';
import { Button } from '../../components/buttons/buttons';
import { AlertError } from '../../components/alerts/alerts';

import { get_family_detail } from '../../api';

const FamilyMemberDetail = (props) => {

    const {
        match: {
            params: {
                id = null,
            }
        } 
    } = props;

    const [alert, setAlert] = useState('');
    const [person, setPerson] = useState({});
    const [loading, setLoading] = useState(true);

    const getData = async() => {
        setAlert(null);
        setLoading(true);
        const {result, error, message, errors} = await get_family_detail(id);
        setLoading(false);

        if(error) {
            setAlert(<AlertError message={message}/>);
        } else {
            setPerson(result.data);
            localStorage.setItem('family_profile', JSON.stringify(result.data));
        }
    }

    useEffect(() => {
        getData();
    }, []);

    return(
        <ContentSection>
            <PageHeader
                ghost={false}
                onBack={() => window.history.back()}
                title="Data Anggota Keluarga"
            />
            <Row>
                <Col lg={24} xs={24} order={{lg: 2, xs: 1}} className="text-center">
                    {alert}
                    <Avatar
                        size={{ xs: 100, sm: 100, md: 100, lg: 100, xl: 100, xxl: 100 }}
                        src={person.profile_pic}
                    /> <br/> <br/>
                    Foto Pasien
                </Col>
                <Col lg={24} xs={24} order={{lg:1, xs:2}}>
                    { loading ? <Skeleton/> : <>
                        <div className="text-right" style={{marginBottom: '10px'}}>
                            <Link to={"/family/member/update/"+id}>
                                <Button type="primary">Update Data Pasien</Button>
                            </Link>
                        </div>
                        <Descriptions size="small" bordered column={{ xs: 1, sm: 1, lg: 2}} >
                            <Descriptions.Item label="Titel" span={2}>{person.title_long} <b>[{person.title_short}]</b></Descriptions.Item>
                            <Descriptions.Item label="Identitas">{person.identity_type}</Descriptions.Item>
                            <Descriptions.Item label="Nomor Identitas">{person.identity_number}</Descriptions.Item>
                            <Descriptions.Item label="Nama Depan">{person.first_name}</Descriptions.Item>
                            <Descriptions.Item label="Nama Belakang">{person.last_name}</Descriptions.Item>
                            <Descriptions.Item label="Tempat Lahir">{person.birth_place}</Descriptions.Item>
                            <Descriptions.Item label="Tanggal Lahir">{person.birth_date_alt} <span className="color-error">({person.age})</span> </Descriptions.Item>
                            <Descriptions.Item label="Jenis Kelamin">{person.gender}</Descriptions.Item>
                            <Descriptions.Item label="Nomor Telepon">{person.phone_number}</Descriptions.Item>
                            <Descriptions.Item label="Agama">{person.religion}</Descriptions.Item>
                            <Descriptions.Item label="Status">{person.married_status}</Descriptions.Item>
                            <Descriptions.Item label="Wilayah Tinggal" span={2}>{person.living_area}</Descriptions.Item>
                            <Descriptions.Item label="Alamat Tinggal" span={2}>{person.address}</Descriptions.Item>
                        </Descriptions>
                    </>}
                </Col>
            </Row>
        </ContentSection>
    );
}

export default FamilyMemberDetail;