import React, { useEffect, useState, Suspense } from 'react';
import { Table, Row, Col, Input, Skeleton, Avatar, PageHeader, Popconfirm, Pagination } from 'antd';
import { TableWrapper, ContentSection } from '../styled';
import { useRouteMatch, useHistory, Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';

// Component
import { Cards } from '../../components/cards/frame/cards-frame';
import { Button } from '../../components/buttons/buttons';
import { AlertError } from '../../components/alerts/alerts';
import Heading from '../../components/heading/heading';

// Api Function
import  { get_family, delete_family_member } from '../../api';

import {
    loadingStart,
    loadingClose,
    loadingContent,
    loadingSuccess,
    loadingError,
    loadingProps
} from '../../redux/loadingmodal/actionCreator';

const List = () => {
    const { path } = useRouteMatch();
    const history = useHistory();
    const dispatch = useDispatch();

    const [alert, setAlert] = useState(null);
    // START: Table event & config
    const [tableLoading, setTableLoading] = useState(true);
    const [source, setSource] = useState([]);
    const [dataCount, setDataCount] = useState(0);
    const [data, setData] = useState({});
    const [filter, setFilter] = useState({ query: null, page: 0, data_per_page: 10,  paginate: true });

    const columns = [
        { title: 'Data', dataIndex: 'data', key: 'data', responsive: ['xs'] },
        { title: 'Nama', dataIndex: 'name', key: 'name', responsive: ['sm'] },
        { title: 'Tanggal Lahir', dataIndex: 'birth_date', key: 'birth_date', responsive: ['sm'] },
        { title: 'Nomor Telepon', dataIndex: 'phone_number', key: 'phone_number', responsive: ['sm'] },
        { title: '#', dataIndex: 'action', key: 'action', width: '150px', responsive: ['sm'] },
    ];

    useEffect(() => {
        getData();
        // eslint-disable-next-line
    }, [filter]);

    const onTableChange = (e) => {
        setFilter({
            ...filter,
            page: e.current,
            data_per_page: e.pageSize,
        });
    }
    // END: Table event & config

    const getData = async () => {
        setTableLoading(true);

        const {result, error, message} = await get_family(filter);
        if(error) {
            setAlert(<AlertError message={message} />);
        } else {
            processData(result.data?.data);
            setData(result.data);
        }

        setTableLoading(false);
    }

    const onPageChange = (e) => {
        setFilter({
            ...filter, page: e
        });
    }

    const delete_person = async (person_id) => {
        dispatch(loadingStart());
        dispatch(loadingContent('Menghapus data...'));

        const {result, error, message, errors, forceStop} = await delete_family_member(person_id);
        if(forceStop) return;

        if(error) {
            dispatch(loadingError());
            dispatch(loadingContent(message));
            dispatch(loadingProps({
                width: '500px'
            }));
            setAlert(<AlertError message={message} />);
            setTimeout(() => dispatch(loadingClose()), 3000);
        } else {
            dispatch(loadingSuccess());
            dispatch(loadingContent('Berhasil menghapus data!'));

            getData();

            setTimeout(() => dispatch(loadingClose()), 2000);
        }
    }

    const processData = (data) => {
        let result = [];
        data.map(row => {
            return result.push({
                key: row.person_id,
                id: row.person_id,
                
                data: (<>
                    <Cards headless={true}>
                        <b>Nama</b> <br/>
                        {row.full_name} <br/>
                        <b>Umur</b> <br/>
                        {row.age} <br/>
                        <b>Nomor Telepon</b> <br/>
                        {row.phone_number} <br/> <br/>

                        <Link to={`${path}/detail/${row.person_id}`}>
                            <Button className="btn-icon" size="default" block={true} size="small" type="primary" title="Detail" onClick={() =>  history.push(`${path}/detail/${row.person_id}`) }>
                                <i aria-hidden="true" className="fa fa-search color-white"></i> Detail
                            </Button>
                        </Link>
                        { !row.primary && (
                            <Popconfirm placement="bottom" title="Yakin menghapus data ini? aksi tidak bisa dibatalkan"  okText="Ya" cancelText="Batal"
                                onConfirm={() =>  delete_person(row.person_id)}
                            > <br/>
                                <Button className="btn-icon" size="default" block={true} size="small" type="danger" title="Hapus">
                                    <i aria-hidden="true" className="fa fa-trash color-white"></i> Hapus
                                </Button>
                            </Popconfirm>
                            ) }
                    </Cards>
                </>),

                name: (
                    <div className="user-info">
                        <figure>
                            <Suspense
                                fallback={
                                    <Skeleton avatar active/>
                                }
                            >
                                <Avatar size={{ xs: 40, sm: 40, md: 40, lg: 40, xl: 40, xxl: 40 }} src={row.profile_pic} />
                            </Suspense>
                        </figure>
                        <figcaption>
                            <Heading className="user-name" as="h6">
                            {row.full_name}
                            </Heading>
                            <span className="user-designation">{row.age}</span>
                        </figcaption>
                    </div>
                ),
                birth_date: row.birth_date,
                phone_number: row.phone_number,
                action: (
                        <>
                            <Link to={`${path}/detail/${row.person_id}`}>
                                <Button className="btn-icon" size="default" shape="round" type="primary" title="Detail" onClick={() =>  history.push(`${path}/detail/${row.person_id}`) }>
                                    <i aria-hidden="true" className="fa fa-search color-white"></i>
                                </Button>
                            </Link> &nbsp;
                            { !row.primary && (
                            <Popconfirm placement="bottom" title="Yakin menghapus data ini? aksi tidak bisa dibatalkan"  okText="Ya" cancelText="Batal"
                                onConfirm={() =>  delete_person(row.person_id)}
                            >
                                <Button className="btn-icon" size="default" shape="round" type="danger" title="Hapus">
                                    <i aria-hidden="true" className="fa fa-trash color-white"></i>
                                </Button>
                            </Popconfirm>
                            ) }
                        </>
                )
            });
        });
        setDataCount(data.total);
        setSource(result);
    }

    return(
        <ContentSection>
            <PageHeader
                ghost={false}
                onBack={() => window.history.back()}
                title="Anggota Keluarga"
                extra={[
                    <Link to="/family/member/add" key="btn_add_family">
                        <Button key="1" type="primary"> Tambah Anggota </Button>
                    </Link>,
                ]}
            />
            {alert}
            <Row gutter={[25, 25]}>
                <Col span={24}>
                    <Cards headless={true} >
                        <div className="table-responsive">
                            <TableWrapper>
                                <Table
                                    loading={tableLoading}
                                    bordered={false}
                                    columns={columns}
                                    dataSource={source}
                                    onChange={onTableChange}
                                    pagination={false} 
                                />
                            </TableWrapper>
                        </div>
                    </Cards>
                </Col>
                <Col span={24} className="text-right">
                    <Pagination
                        defaultCurrent={data?.current_page}
                        total={data?.total}
                        pageSize={filter?.data_per_page}
                        onChange={onPageChange}
                        showQuickJumper={true}
                        showTotal={total => `Total ${total} data`}
                    />
                </Col>
            </Row>
        </ContentSection>
    );
}

export default List;