import React, { useEffect, useState } from 'react';
import Cookies from 'js-cookie';
import { useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
import { Avatar, Form, Input, Button, Row,  Col } from 'antd';

import LoginIcon from '../../../static/img/password.png';
import { login, resend_verification } from '../../../api';
import { AlertError } from '../../alerts/alerts';

import { showLoginModal } from '../../../redux/authentication/actionCreator';

import {
    loadingStart, loadingError, loadingSuccess, loadingContent, loadingClose
} from '../../../redux/loadingmodal/actionCreator';

const Login = (props) => {

    const {
        compact = false,
        logUser = {},
        relogin = false,
        // ...otherProps
    } = props;

    const dispatch = useDispatch();
    const [alert, setAlert] = useState('');

    const [form] = Form.useForm();

    const formSize = compact ? 24 : {span: 12, offset: 6};

    const submitForm = async (e) => {

        setAlert('');
        dispatch(loadingStart());
        dispatch(loadingContent('memproses login...'));

        let _to = setTimeout(() => dispatch(loadingContent('proses sedang berlangsung...')), 5000);

        const {result, error, message} = await login(e);

        clearTimeout(_to);

        if(error) {
            let _message = message;
            if(result?.need_verification) {
                _message = (<>
                Silahkan verifikasi akun anda! <br/>
                <small><a className="text-blue" onClick={resendVerificationLink}>Kirim ulang link verifikasi</a></small>
                </>);
            }
            dispatch(loadingClose());
            setAlert(<AlertError message={_message}/>);
        } else {
            dispatch(loadingSuccess());
            dispatch(loadingContent('Login berhasil! anda akan dialihkan'));
            Cookies.set('token', result.token);
            if(result.user) {
                localStorage.setItem('personData', JSON.stringify(result.user));
            }
            setTimeout(() => {
                if(relogin) {
                    dispatch(loadingClose());
                    dispatch(showLoginModal(false));
                } else {
                    window.location =  result?.redirect ? result.redirect : '/home';
                }
            }, 3000);
        }

    }

    const resendVerificationLink = async () => {
        setAlert('');
        
        dispatch(loadingStart());
        dispatch(loadingContent('mengirim link verifikasi...'));

        const {error, message, errors} = await resend_verification({
            contact: form.getFieldValue('contact')
        });
        
        if(error) {
            dispatch(loadingClose());
            setAlert(<AlertError message={message}/>);
            form.setFields(errors);
        } else {
            dispatch(loadingSuccess());
            dispatch(loadingContent('Link verifikasi telah dikirim, silahkan cek email/nomor whatsapp yang anda daftarkan.'));
            setTimeout( dispatch(loadingClose()) , 3000);
        }
    }

    return(<>
        <div className="text-center">
            <Avatar size={{ xs: 80, sm: 80, md: 80, lg: 80, xl: 80, xxl: 100 }} src={LoginIcon} />
        </div> <br/>
        <Row  gutter={25} className="text-left">
            <Col lg={formSize} xs={24}>
                {alert}
                <Form layout="vertical" form={form} onFinish={submitForm}>
                    <Form.Item name="contact" initialValue={logUser?.email} label="Email/Nomor Telepon"
                        rules={[{required: true, message: 'Masukan email atau nomor telepon'}]}
                    >
                        <Input placeholder="..." autoFocus />
                    </Form.Item>
                    <Form.Item name="password" label="Password"
                        rules={[{required: true, message: 'Masukan password'}]}
                    >
                        <Input.Password placeholder="..." autoComplete="false"/>
                    </Form.Item>
                    <Form.Item>
                        <Button type="primary" htmlType="submit" size="large" className="login-form-button" block={true}>
                            {!relogin ? 'Login' : 'Autentikasi'}
                        </Button> 
                        { compact && (<><br/><br/>
                        <Link to="/register">
                            <Button type="danger" block={true}>Register</Button>
                        </Link></>)}
                    </Form.Item>
                </Form>
            </Col>
        </Row>
    </>);
}

export default Login;