import React, { useEffect, useState } from 'react';
import { Row, Col, PageHeader, Skeleton } from 'antd';
import { GalleryCard, ContentSection } from '../styled';
import Heading from '../../components/heading/heading';

import { get_news_detail } from '../../api';

const Detail = (props) => {

    const [news_id, setNewsId] = useState(props?.match?.params?.id);
    const [data, setData] = useState({});
    const [loading, setLoading] = useState(true);
    const [bannerType, setBannerType] = useState('little');

    const getData = async() => {
        setLoading(true);
        const {result, error, message, errors} = await get_news_detail(news_id);
        setLoading(false);

        if(error){
            console.log('err:', message);
        } else {
            setData(result.data);
            if(result.data.thumbnail_long) {
                setBannerType('long');
            }
        }
    }

    useEffect(() => {
        getData();
    }, []);

    return(
        <ContentSection>
            <PageHeader
                ghost={false}
                onBack={() => window.history.back()}
                title={"Berita: " + news_id }
            />
            <div>
                <Row gutter={[25, 25]}>
                    {loading && (<Col lg={24}><Skeleton/></Col>)}
                    <Col
                        lg={{ span: 6, offset: 0, order: 2 }}
                        className="text-center"
                    >
                        <GalleryCard style={{ marginBottom: '25px' }}>
                            <figure>
                                <img style={{ width: '100%' }} src={data.thumbnail} alt="" />
                            </figure>
                        </GalleryCard>
                    </Col>
                    <Col lg={{
                        span: 18,
                        order: 1
                    }}>
                        <Heading as="h3">{data.title}</Heading>
                        {data.information}
                    </Col>
                </Row>
            </div>
        </ContentSection>
    );
}

export default Detail;