import React from 'react';
import queryString from 'query-string';
import { withRouter } from "react-router-dom";
import { PageHeader, Row, Col, Select, Image, Form, Input  } from 'antd';

import { ContentSection } from '../styled';
import { get_family } from '../../api';
import { Button } from '../../components/buttons/buttons';

import { registerStorage, registerStorageAddItem } from '../../utility/localStorageControl';

class FormInformation extends React.Component {

    params = queryString.parse(window.location.search);
    formRef = React.createRef();
    storageKey = 'registerObj';

    state = {
        data: {},
        arr_family: [],
        person: { profile_pic: 'error' },
        loadingTime: false
    }

    async getData() {
        const {result, error, message} = await get_family();

        if(error) {
            console.log('err:', message);
        } else {
            this.setState({
                ...this.state, arr_family: result.data
            },() => {
                let logRegister = registerStorage();
                if(logRegister?.person_id) {
                    this.formRef.current.setFieldsValue(logRegister);
                    this.setStatePerson(logRegister.person_id);
                }
            });


        }
    }

    componentDidMount() {
        this.getData();
    }

    setStatePerson(person_id) {
        let person = this.state.arr_family.find(row => row.person_id === person_id);
        this.setState({
            ...this.state,
            person: person
        },() => {
            registerStorageAddItem('personObj', JSON.stringify(person));
            console.log('register personObj:', person);
        });
    }

    valuesChange(e) {
        let key = Object.keys(e)[0];

        if(key === 'person_id') {
            console.log('person_id:', e);
            this.setStatePerson(e[key]);
        }
        registerStorageAddItem(key, e[key]);
    }

    formSubmit (e) {
        return this.props?.history.push('/appointment/register/confirmation');
    }

    render() {

        return(
            <ContentSection>
                <PageHeader
                    ghost={false}
                    onBack={() => this.props?.history.push('/appointment/register/schedule')}
                    title="Langkah 3/4: Isi Formulir Pasien"
                /> <br/> <br/>
                <Form
                    layout="vertical"
                    ref={this.formRef}
                    onValuesChange={this.valuesChange.bind(this)}
                    onFinish={this.formSubmit.bind(this)}
                    >
                    <Row>
                        <Col lg={12} xs={{
                            span: 24,
                            order: 2
                        }} xs={24}>
                            <Row gutter={25}>
                                <Col lg={24} xs={24}>
                                    <Form.Item
                                        label="Nama Pasien"
                                        name="person_id"
                                        rules={[
                                            { required: true, message: 'Pilih Pasien' }
                                        ]}
                                    >
                                        <Select placeholder="Pilih pasien" >
                                            {   this.state.arr_family.map(row => {
                                                    return <Select.Option key={row.person_id} value={row.person_id
                                                    }>{row.full_name}</Select.Option>
                                                })
                                            }
                                        </Select>
                                    </Form.Item> <br/>
                                </Col>

                                <Col lg={24} xs={24}>
                                    <Form.Item
                                        label="Keluhan Utama"
                                        name="main_complaint"
                                        rules={[
                                            { required: true, message: 'Masukan keluhan utama' }
                                        ]}
                                    >
                                        <Input.TextArea placeholder="..." rows={5} />
                                    </Form.Item> <br/>
                                </Col>


                                <Col lg={24} xs={24}>
                                    <Form.Item
                                        label="Riwayat Penyakit"
                                        name="disease_history"
                                    >
                                        <Input.TextArea placeholder="..." rows={3} />
                                    </Form.Item> <br/>
                                </Col>


                                <Col lg={24} xs={24}>
                                    <Form.Item
                                        label="Alergi"
                                        name="allergy"
                                    >
                                        <Input.TextArea placeholder="..." rows={3} />
                                    </Form.Item> <br/>
                                </Col>

                                <Col lg={12} xs={24}>
                                    <Form.Item
                                        label="Suhu Badan"
                                        name="body_temperature"
                                    >
                                        <Input type="number" placeholder="..." addonAfter="℃" />
                                    </Form.Item>
                                </Col>

                                <Col lg={12} xs={24}>
                                    <Form.Item
                                        label="Tekanan Darah"
                                        name="blood_pressure"
                                    >
                                        <Input placeholder="..." addonAfter="mmHg" />
                                    </Form.Item>  <br/>
                                </Col>

                                <Col lg={12} xs={24}>
                                    <Form.Item
                                        label="Berat Badan"
                                        name="weight"
                                    >
                                        <Input type="number" placeholder="..." addonAfter="Kg" />
                                    </Form.Item>
                                </Col>

                                <Col lg={12} xs={24}>
                                    <Form.Item
                                        label="Tinggi Badan"
                                        name="height"
                                    >
                                        <Input type="number" placeholder="..." addonAfter="Cm" />
                                    </Form.Item>
                                </Col>

                                <Col lg={24}> <br/>
                                    <Form.Item className="text-center">
                                        <Button type="primary" size="large" htmlType="submit" block={true}>
                                            Selanjutnya &nbsp; <i className="fa fa-arrow-circle-right" aria-hidden={true}></i>
                                        </Button>
                                    </Form.Item>
                                </Col>
                            </Row>
                        </Col>

                        <Col lg={12} xs={{
                            span: 24,
                            order: 1
                        }}>
                            <Form.Item label={<div></div>} className="text-center">
                                <Image
                                    src={this.state.person.profile_pic}
                                    fallback="data:image/png;base64,"
                                /> <br/>
                                {this.state.person?.full_name} <br/>
                                <span className="color-danger">{this.state.person?.age}</span>
                            </Form.Item>
                        </Col>
                
                    </Row>
                </Form>
            </ContentSection>
        );
    }
}

export default withRouter (FormInformation);