import React from 'react';
import { Row, Col, PageHeader, Descriptions } from 'antd';
import { ContentSection } from '../styled';
import ReactMidtrans from 'react-midtrans/src/index';
import { Link } from 'react-router-dom';

// Component
import { Cards } from '../../components/cards/frame/cards-frame';
import { Button } from '../../components/buttons/buttons';
import { AlertError } from '../../components/alerts/alerts';

import { get_bill_detail } from '../../api';
import { format_rupiah, payment_label } from '../../utility/utility';

const Detail = (props) => {

    const { id } = props.match.params;
    const [loading, setLoading] = React.useState(true);
    const [alert, setAlert] = React.useState(null);
    const [data, setData]  = React.useState({});

    const getData = async() => {
        setLoading(true);

        const {
            result, error, message
        } = await get_bill_detail(id);

        if(error) {
            setAlert(<AlertError message={message}/>);
        } else {
            setData(result.data);
        }

        setLoading(false);
    }

    React.useEffect(() => {
        getData();
    }, []);

    return(
        <ContentSection>
            <PageHeader
                ghost={false}
                onBack={() => window.history.back()}
                title={`Informasi Tagihan #${id}`}
            />
            {alert}
            <Row gutter={[25, 25]}>
                <Col xs={24} lg={{
                    span: 18,
                    offset: 3
                }}> <br />
                    <Descriptions  bordered column={1}>
                        <Descriptions.Item label="ID Tagihan">#{id}</Descriptions.Item>
                        <Descriptions.Item label="ID Konsultasi">
                            <Link to={`/appointment_history/${data.aid}`}>
                                #{data.aid}
                            </Link>
                        </Descriptions.Item>
                        <Descriptions.Item label="Tanggal Konsultasi">{data.id_consul_date} {data.consul_time}</Descriptions.Item>
                        <Descriptions.Item label="Dokter">{data.doctor}</Descriptions.Item>
                        <Descriptions.Item label="Pasien">{data.patient}</Descriptions.Item>
                        <Descriptions.Item label="Nominal">{format_rupiah(data.amount, true)}</Descriptions.Item>
                        <Descriptions.Item label="Tanggal Melakukan Pendaftaran">{data.created_at}</Descriptions.Item>
                        <Descriptions.Item label="Tenggat Pembayaran">{data.expired_at}</Descriptions.Item>
                        <Descriptions.Item label="Status">{payment_label(data.status)}</Descriptions.Item>
                    </Descriptions>
                    { data.status === 'waiting_payment' && (
                        <>
                        <br />
                        <br />
                        <ReactMidtrans
                            clienttKey={data?.payment_key}
                            token={data.snaptoken}
                        >
                            <Button type="primary" block={true} size="large">Bayar Tagihan</Button>
                        </ReactMidtrans>
                        </>
                    ) }
                </Col>
            </Row>
        </ContentSection>
    );
}

export default Detail;