import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom";
import { PageHeader, Row, Col, Descriptions, Image } from 'antd';
import ReactMidtrans from 'react-midtrans/src/index';

import { Countdown } from '../../components/countdown';
import { ContentSection } from '../styled';
import { Cards } from '../../components/cards/frame/cards-frame';
import Heading from '../../components/heading/heading';
import { Button, BtnLabel } from '../../components/buttons/buttons';
import Loading from '../../components/loadings';
import { format_rupiah } from '../../utility/utility';

import { get_appointment_detail, re_register } from '../../api';

import {
    loadingStart,
    loadingContent,
    loadingSuccess,
    loadingError,
    loadingClose,
    loadingProps
} from '../../redux/loadingmodal/actionCreator';


class Detail extends React.Component {

    state = {
        appointment_id: null,
        loading: true,
        error: null,
        appointment: {},
    };

    dispatch = this.props.dispatch;

    componentDidMount() {
        this.setState({
            ...this.state,
            appointment_id: this.props?.match?.params?.id
        },() => {
            this.getData();
        });
    }

    getData = async () => {
        const {result, error, message} = await get_appointment_detail(this.state.appointment_id);

        if(error) {
            this.setState({
                ...this.state, loading: false, error: message
            });
        } else {
            this.setState({
                ...this.state, loading: false, error: null, appointment: result.data
            });
        }
    }

    re_register = async () => {
        this.dispatch(loadingStart());
        this.dispatch(loadingContent('Melakukan daftar ulang...'));
        
        const {result, error, message} = await re_register(this.state.appointment_id);

        if(error) {
            this.dispatch(loadingError());
            this.dispatch(loadingContent(message));
            setTimeout(() => this.dispatch(loadingClose()), 3000);
        } else {
            this.dispatch(loadingSuccess());
            this.dispatch(loadingContent(message));
            this.setState({
                ...this.state, appointment_id: result.data.appointment.id
            },() => {
                this.props?.history.push('/appointment_history/' + this.state.appointment_id);
                this.getData();
            });
            setTimeout(() => this.dispatch(loadingClose()), 3000);
        }
    }

    midtransOnSuccess = (e)  => {
        console.log('midtransOnSuccess:', e);
    }

    midtransOnPending = (e)  => {
        console.log('midtransOnPending:', e);
    }

    midtransOnError = (e)  => {
        console.log('midtransOnError:', e);
    }

    midtransOnClose = (e)  => {
        console.log('midtransOnClose:', e);
    }

    render() {

        const LoadingComponent = () => {
            return(<div className="text-center">
                <Loading/> <br/>
                Mengambil  data perjanjian...
            </div>);
        }

        const ErrorComponent = () => {
            return(<div className="text-center">
                <Loading status="error"/> <br/>
                <Heading>Data perjanjian tidak ditemukan</Heading>
                { this.state.error === true ? 'kembali dan pastikan anda memilih perjanjian dari list yang tersedia' : this.state.error }
            </div>);
        }

        const DetailInformation = () => {
            return(<>
                <Row gutter={8}>
                    <Col lg={12} xs={24}>
                        <Cards headless>
                            <figure className="text-center">
                                <Image
                                    width={200}
                                    preview={false}
                                    src={this.state?.appointment?.doctor_pic}
                                />
                            </figure>
                            <Descriptions title="Jadwal Telekonsultasi" column={1} bordered>
                                <Descriptions.Item label="Dokter">{this.state.appointment?.doctor_name}</Descriptions.Item>
                                <Descriptions.Item label="Departemen">{this.state.appointment?.department}</Descriptions.Item>
                                <Descriptions.Item label="Cabang">{this.state.appointment?.branch}</Descriptions.Item>
                                <Descriptions.Item label="Tanggal">{this.state.appointment?.id_consul_date}</Descriptions.Item>
                                <Descriptions.Item label="Jam">{this.state.appointment?.consul_time}</Descriptions.Item>
                                <Descriptions.Item label="Durasi">{this.state.appointment?.duration} Menit</Descriptions.Item>
                                <Descriptions.Item label="Tarif">
                                    <span className="color-error">Rp {format_rupiah(this.state.appointment?.fee)}</span>
                                </Descriptions.Item>
                                <Descriptions.Item label="Status">
                                    <BtnLabel type={this.state.appointment?.status}/>
                                </Descriptions.Item>
                                {   (this.state.appointment.status === 'payment_cancel' || this.state.appointment.status === 'payment_expire') && this.state.appointment.can_re_register && (
                                        <>
                                        <Descriptions.Item label="Daftar Ulang">
                                            <Button type="primary" onClick={this.re_register.bind(this)}>Daftar Ulang</Button>
                                        </Descriptions.Item>
                                        </>
                                    )
                                }
                                { this.state.appointment.status === 'done' && (
                                    <>
                                    <Descriptions.Item label="Jam Dimulai">{this.state.appointment?.start_consul}</Descriptions.Item>
                                    <Descriptions.Item label="Jam Selesai">{this.state.appointment?.end_consul}</Descriptions.Item>
                                    </>
                                ) }
                                {   this.state.appointment.status === 'waiting_payment' && (
                                        <Descriptions.Item label="Batas Pembayaran">
                                            {this.state.appointment?.payment_expired_at} <br/>
                                            <Countdown date={this.state.appointment?.payment_expired_at}/>
                                        </Descriptions.Item>
                                    )
                                }

                                {   this.state.appointment.status === 'waiting_consul' && (
                                        <Descriptions.Item label="Konsultasi Dimulai Dalam">
                                            {this.state.appointment?.consul_date} {this.state.appointment?.consul_time} <br/>
                                            <Countdown date={this.state.appointment?.consul_date} time={this.state.appointment?.consul_time}/>
                                        </Descriptions.Item>
                                    )
                                }
                            </Descriptions>
                        </Cards>
                    </Col>
                    
                    <Col lg={12} xs={24}>
                        <Cards headless>
                            <figure className="text-center">
                                <Image
                                    width={200}
                                    preview={false}
                                    src={this.state?.appointment?.patient_pic}
                                />
                            </figure>
                            <Descriptions title="Informasi Pasien" column={1} bordered>
                                <Descriptions.Item label="Nama">{this.state.appointment?.patient_name}</Descriptions.Item>
                                <Descriptions.Item label="Umur">{this.state.appointment?.age}</Descriptions.Item>
                                <Descriptions.Item label="Keluhan Utama">{this.state.appointment?.main_complaint}</Descriptions.Item>
                                <Descriptions.Item label="Riwayat Penyakit">{this.state.appointment?.disease_history ? this.state.appointment?.disease_history : '-'}</Descriptions.Item>
                                <Descriptions.Item label="Alergi">{this.state.appointment?.allergy ? this.state.appointment?.allergy : '-'}</Descriptions.Item>
                                <Descriptions.Item label="Suhu Badan">{this.state.appointment?.body_temperature ? this.state.appointment?.body_temperature : '-'} ℃</Descriptions.Item>
                                <Descriptions.Item label="Tekanan Darah">{this.state.appointment?.blood_pressure ? this.state.appointment?.blood_pressure : '- '} mmHg</Descriptions.Item>
                                <Descriptions.Item label="Berat Badan">{this.state.appointment?.weight ? this.state.appointment?.weight : '-'} Kg</Descriptions.Item>
                                <Descriptions.Item label="Tinggi Badan">{this.state.appointment?.height ? this.state.appointment?.height : '-'} Cm</Descriptions.Item>
                            </Descriptions>
                        </Cards>
                    </Col>

                    { this.state.appointment?.status === 'waiting_payment' && this.state.appointment?.payment_key && (<>
                        <Col lg={24}>
                            <ReactMidtrans
                                clienttKey={this.state.appointment.payment_key}
                                token={this.state.appointment.snaptoken}
                                onSuccess={this.midtransOnSuccess.bind(this)}
                                onPending={this.midtransOnPending.bind(this)}
                                onError={this.midtransOnError.bind(this)}
                                onClose={this.midtransOnClose.bind(this)}
                            >
                                <Button type="primary" block={true} size="large">Bayar Tagihan</Button>
                            </ReactMidtrans>
                        </Col>
                    </>)}

                    { this.state.appointment?.status === 'waiting_consul' && (<>
                        <Col lg={24}>
                            { this.state?.appointment?.join_url ? 
                                <a href={this.state.appointment.join_url} target="__blank">
                                    <Button type="primary" block={true} size="large">Mulai Konsultasi</Button>
                                </a>
                            :
                                <Button type="warning" block={true} size="large">Link Konsultasi Belum Tersedia</Button>    
                            }
                        </Col>
                    </>)}
                </Row>
            </>);
        }

        return(
            <ContentSection>
                <PageHeader
                    ghost={false}
                    onBack={() => window.history.back()}
                    title={"Pendaftaran: " + this.state.appointment_id}
                />
                { this.state.loading ? <LoadingComponent/> : this.state.error ? <ErrorComponent/> : <DetailInformation/>}
            </ContentSection>
        );
    }
}

export default connect()(withRouter(Detail));