import React, { useEffect, useState } from 'react';
import { Row, Col, PageHeader, Skeleton, Pagination } from 'antd';
import { Link } from 'react-router-dom';
import { GalleryCard, ContentSection } from '../styled';
import { AlertError } from '../../components/alerts/alerts';
import { get_news_list } from '../../api';

const List = (props) => {

    const [item, setItem] = useState([]);
    const [loading, setLoading] = useState(true);
    const [alert, setAlert] = useState(null);
    const [data, setData] = useState({});
    const [filter, setFilter] = useState({data_per_page: 20, page: 1});

    const getData = async() => {
        setLoading(true);
        const {result, error, message} = await get_news_list();
        setLoading(false);
        if(error) {
            setAlert(<AlertError message={message} />);
        } else {
            setData(result.data);
            setItem(result.data.data);
        }
    }

    const onPageChange = (e) => {
        setFilter({
            ...filter, page: e
        });
        getData();
    }

    useEffect(() => {
        getData();
    }, [])

    return(
        <ContentSection>
            <PageHeader
                ghost={false}
                onBack={() => window.history.back()}
                title="Berita"
            /> <br/>
            <Row gutter={[25, 25]}>
                {alert && (<Col lg={24}>{alert}</Col>)}
                {loading ? (<>
                    <Col lg={6} xs={12}><Skeleton/></Col>
                    <Col lg={6} xs={12}><Skeleton/></Col>
                    <Col lg={6} xs={12}><Skeleton/></Col>
                    <Col lg={6} xs={12}><Skeleton/></Col>
                </>) :
                    item.map(row => {
                        return <Col lg={6} xs={12} key={row.news_id}>
                            <Link to={"/news/" + row.news_id}>
                                <GalleryCard style={{ marginBottom: '25px' }}>
                                    <figure>
                                        <img style={{ width: '100%' }} src={row.thumbnail} alt="" />
                                        <figcaption>
                                            <b className="color-dark"> <br/>
                                            {row.title}
                                            </b>
                                        </figcaption>
                                    </figure>
                                </GalleryCard>
                            </Link>
                        </Col>
                    })
                }
                <Col span={24} className="text-right">
                    <Pagination
                        defaultCurrent={data?.current_page}
                        total={data?.total}
                        pageSize={20}
                        onChange={onPageChange}
                        showQuickJumper={true}
                        showTotal={total => `Total ${total} data`}
                    />
                </Col>
            </Row>
        </ContentSection>
    );
}

export default List;