import React, { useEffect, useState, useRef } from 'react';
import { Select } from 'antd';

import { titleConfig, religionConfig, MarriedStatusConfig } from '../config';
import { get_identitytype, get_livingarea, get_branch, get_department } from '../../../api';

export const SelectTitle = (props) => {

    const {
        searchable = false,
        ...otherProps
    } = props;

    return(
        <Select
            showSearch={ searchable ? true : false }
            className="sDash_fullwidth-select"
            placeholder="Pilih titel"
            {...otherProps}
        >
            {   Object.keys(titleConfig).map(index => {
                    let row = titleConfig[index];
                    return <Select.Option key={index} value={parseInt(index)}>{row.full} ({row.short})</Select.Option>;
                })
            }
        </Select>
    );
}

export class SelectIdentityType extends React.Component {

    state = {
        data: [],
        mounted: false
    }
    
    getData = async () => {
        this.setState({
            ...this.state, mounted: true
        });
        const {result, error, message} = await get_identitytype({all_data: true});

        if(error) {
            console.log('[ERROR] SelectIdentityType:', message);
        } else {
            this.setState({
                ...this.state, data:  result.data
            });
        }
    }

    componentDidUpdate() {
    }

    componentDidMount() {
        if(this.state.data.length === 0) {
            this.getData();
        }
    }

    render() {

        return (
            <Select {...this.props}>
                {   this.state.data.map(row => {
                        return <Select.Option key={row.itid} value={parseInt(row.itid)}>{row.name}</Select.Option>
                    })
                }
            </Select>
        );
    }
}

export class SelectLivingArea extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            filters: {
                village_id: null,
                query: null,
                all_data: false
            },
            village_id: 0,
            data: [],
            notFoundContent: 'Data tidak ditemukan',
        }
        this.timeout = null;
        this.getData = this.getData.bind(this);
    }
    
    componentDidUpdate() {
        if(!this.props.value) return false;
        if(
            this.props.value
            && !this.state.filters.village_id
            && this.props.value != this.state.village_id
        ) {
            this.setState({
                ...this.state,
                filters: {
                    all_data: true,
                    query: null,
                    village_id: this.props.value
                }
            },() => {
                this.getData();
            });
        }
    }

    componentDidMount() {
        this.getData();
    }

    getData = async() =>  {
        const{result} = await get_livingarea({...this.state.filters});
        if(!result?.status) {
            this.setState({...this.state, notFoundContent: 'Data tidak ditemukan...'})
        } else {
            this.setState({
                ...this.state,
                data: result?.data?.data,
                notFoundContent: (!result?.data?.data?.length ? 'Data tidak ditemukan' : '')
            });
        }
    }

    
    render() {
        const handleSearch = (value) => {
            if(!value) return;
            this.setState({...this.state, notFoundContent: 'Mencari data...'})
            if(this.timeout) {
                clearTimeout(this.timeout);
                this.timeout = null;
            }
            
            const search = ()  => {
                this.setState({...this.state, filters: {
                    ...this.state.filters,
                    village_id: null,
                    query: value,
                }},() => {
                    this.getData();
                });
            }

            this.timeout = setTimeout(search, 500);
        }

        const selectOption = (e) => {
            this.setState({
                ...this.state, village_id: e
            });
        }
        
        return(
            <Select {...this.props}
                allowClear={true}
                showSearch={true}
                filterOption={(input, option) =>
                    option.children.join('').toLowerCase().indexOf(input.toLowerCase()) >= 0
                }
                onSearch={handleSearch}
                notFoundContent={this.state.notFoundContent}
                onSelect={selectOption}
            >
                {   Object.keys(this.state.data).map(rowIndex => {
                        let row = this.state.data[rowIndex];
                        return <Select.Option key={row.village_id} value={row.village_id}>
                            {row.village}/ {row.distric}/ {row.city}/ {row.province}
                        </Select.Option>
                    })
                }
            </Select>
        );
    }
}

