import React from 'react';
import { Menu } from 'antd';
import { useDispatch } from 'react-redux';
import { NavLink, useRouteMatch } from 'react-router-dom';
import FeatherIcon from 'feather-icons-react';
import propTypes from 'prop-types';
import Cookies from 'js-cookie';

const MenuItems = ({ darkMode, toggleCollapsed, topMenu, ...otherProps }) => {
  const { path } = useRouteMatch();

  const pathName = window.location.pathname;
  const pathArray = pathName.split(path);
  const mainPath = pathArray[0];
  const dispatch = useDispatch();
  const mainPathSplit = mainPath.split('/');

  const {
    homepage = false,
    login = null,
    register = null
  } = otherProps;

  const [openKeys, setOpenKeys] = React.useState(
    !topMenu ? [`${mainPathSplit.length > 2 ? mainPathSplit[1] : 'dashboard'}`] : [],
  );

  const onOpenChange = keys => {
    setOpenKeys(keys[keys.length - 1] !== 'recharts' ? [keys.length && keys[keys.length - 1]] : keys);
  };

  const onClick = item => {
    if (item.keyPath.length === 1) setOpenKeys([]);
  };

  const SignOut = (e) => {
    localStorage.clear();
    Cookies.remove('token');
    window.location = '/auth';
  }

  return (
    <Menu
      onOpenChange={onOpenChange}
      onClick={onClick}
      mode={!topMenu || window.innerWidth <= 991 ? 'inline' : 'horizontal'}
      theme={darkMode && 'dark'}
      // // eslint-disable-next-line no-nested-ternary
      defaultSelectedKeys={[
        `${
          mainPathSplit.length === 1 ? 'home' : mainPathSplit.length === 2 ? mainPathSplit[1] : mainPathSplit[2]
        }`,
      ]}
      defaultOpenKeys={!topMenu ? [`${mainPathSplit.length > 2 ? mainPathSplit[1] : 'dashboard'}`] : []}
      overflowedIndicator={<FeatherIcon icon="more-vertical" />}
      openKeys={openKeys}
    >
      { homepage ? (<>
        <Menu.Item key="home">
          <NavLink onClick={toggleCollapsed} to={`${path}`}>
            Homepage
          </NavLink>
        </Menu.Item>

        <Menu.Item key="login" onClick={login}>
          <NavLink to="#">
            Login
          </NavLink>
        </Menu.Item>

        <Menu.Item key="register" onClick={register}>
          <NavLink to="#">
            Daftar
          </NavLink>
        </Menu.Item>
      </>) : (<>
        <Menu.Item key="home">
          <NavLink onClick={toggleCollapsed} to={`/home`}>
            Home
          </NavLink>
        </Menu.Item>

        <Menu.Item key="appointment">
            <NavLink  onClick={toggleCollapsed} to="/appointment/schedule">
                Pendaftaran Baru
            </NavLink>
        </Menu.Item>
        <Menu.Item key="appointment_history">
            <NavLink  onClick={toggleCollapsed} to="/appointment_history">
                Riwayat Pendaftaran
            </NavLink>
        </Menu.Item>
        <Menu.Item key="bills">
            <NavLink  onClick={toggleCollapsed} to="/bills">
                Tagihan
            </NavLink>
        </Menu.Item>
        <Menu.Item key="family_member">
            <NavLink  onClick={toggleCollapsed} to="/family/member">
                Anggota Keluarga
            </NavLink>
        </Menu.Item>
        <Menu.Item key="news">
            <NavLink  onClick={toggleCollapsed} to="/news">
                Berita
            </NavLink>
        </Menu.Item>
        <Menu.Item key="setting">
            <NavLink onClick={toggleCollapsed} to="/profile">
                Pengaturan Akun
            </NavLink>
        </Menu.Item>
        <Menu.Item key="logout">
            <NavLink onClick={toggleCollapsed} to="#!" onClick={SignOut}>
                keluar
            </NavLink>
        </Menu.Item>
      </>) }
    </Menu>
  );
};

MenuItems.propTypes = {
  darkMode: propTypes.bool,
  topMenu: propTypes.bool,
  toggleCollapsed: propTypes.func,
};

export default MenuItems;
