import React, { useState } from 'react';
import { Form, Input, Button, Row,  Col } from 'antd';
import { useDispatch } from 'react-redux';

import { reset_password } from '../../../api';
import { AlertError } from '../../alerts/alerts';

import {
    loadingStart,
    loadingClose,
    loadingContent,
    loadingSuccess
} from '../../../redux/loadingmodal/actionCreator';

const RegisterForm = (props) => {

    const dispatch = useDispatch();
    const [alert, setAlert] = useState('');

    const [form] = Form.useForm();

    const submitForm = async (e) => {

        setAlert(null);
        dispatch(loadingStart());
        dispatch(loadingContent('Memproses permintaan...'));

        let _to = setTimeout(() => dispatch(loadingContent('sedang memproses...')), 5000);

        const {error, message, errors} = await reset_password(e);

        clearTimeout(_to);

        if(error) {
            dispatch(loadingClose());
            setAlert(<AlertError message={message} />);
            form.setFields(errors);
        } else {
            dispatch(loadingSuccess());
            dispatch(loadingContent('Password baru akan dikirim ke email/whatsapp anda jika akun anda terdaftar di database kami'));
            setTimeout(() => window.location = '/auth', 5000);
        }

    }

    return(
        <Row  gutter={25} className="text-left">
                <Col lg={24} xs={24}>
                    {alert}
                    <Form layout="vertical" form={form} onFinish={submitForm}>
                        <Row gutter={25}>
                            <Col lg={24} xs={24}>
                                <Form.Item name="contact" label="Alamat Email/No Telepon"
                                    rules={[{required: true, message: 'Masukan email/nomor whatsapp'}]}
                                >
                                    <Input placeholder="..." autoFocus />
                                </Form.Item>
                            </Col>
                        </Row>
                        
                        <Form.Item>
                            <Button type="primary" size="large" htmlType="submit" className="login-form-button" block={true}>
                            Reset Password
                            </Button>
                        </Form.Item>
                    </Form>
                </Col>
            </Row>
        );
}

export default RegisterForm;