import React, { useState, useEffect } from 'react';
import { NavLink, useRouteMatch } from 'react-router-dom';
import { Menu } from 'antd';
import { logOut } from '../../redux/authentication/actionCreator';
import { UsergroupAddOutlined, FormOutlined, BookOutlined, HomeOutlined, SettingOutlined, NotificationOutlined, ShoppingOutlined } from '@ant-design/icons';
import { useDispatch } from 'react-redux';
const { SubMenu } = Menu;

export const MenuList = (props) => {
    const { url } = useRouteMatch();
    const dispatch = useDispatch();
    const [current, setCurrent] = useState();
  
    const SignOut = (e) => {
      e.preventDefault();
      localStorage.clear();
      dispatch(logOut());
      window.location = '/auth';
    }

    useEffect(() => {

        const keys = {
            home: 'home',
            appointment: 'appointment',
            family: 'family_member',
            regitration: 'regitration_history',
            schedule: 'schedule',
            profile: 'setting',
            appointment_history: 'appointment_history',
            news: 'news'
        }

        let first_uri = url.split('/');
            first_uri = typeof first_uri[1] !== 'undefined' ? first_uri[1] : 'home';

        setCurrent(keys[first_uri]);
        }, [url]);

    return(
        <Menu mode="horizontal" className="menu_desktop" onClick={(e) => setCurrent(e.key)} selectedKeys={[current]}>
            <Menu.Item key="home" icon={<HomeOutlined />}>
                <NavLink to="/home">
                    Home
                </NavLink>
            </Menu.Item>
            <Menu.Item key="appointment" icon={<FormOutlined />}>
                <NavLink to="/appointment/schedule">
                    Pendaftaran Baru
                </NavLink>
            </Menu.Item>
            <Menu.Item key="appointment_history" icon={<BookOutlined />}>
                <NavLink to="/appointment_history">
                    Riwayat Pendaftaran
                </NavLink>
            </Menu.Item>
            <Menu.Item key="bills" icon={<ShoppingOutlined />}>
                <NavLink to="/bills">
                    Tagihan
                </NavLink>
            </Menu.Item>
            <Menu.Item key="family_member" icon={<UsergroupAddOutlined />}>
                <NavLink to="/family/member">
                    Anggota Keluarga
                </NavLink>
            </Menu.Item>
            <Menu.Item key="news" icon={<NotificationOutlined />}>
                <NavLink to="/news">
                    Berita
                </NavLink>
            </Menu.Item>
            <SubMenu key="SubMenu" icon={<SettingOutlined />} title="Akun">
                <Menu.Item key="setting">
                    <NavLink to="/profile">
                        Pengaturan Akun
                    </NavLink>
                </Menu.Item>
                <Menu.Item key="logout">
                    <NavLink to="#!" onClick={SignOut}>
                        keluar
                    </NavLink>
                </Menu.Item>
            </SubMenu>
        </Menu>
    );
}