import Axios from 'axios';
import Cookies from 'js-cookie';
import store from '../redux/store';
import { loginModal } from '../redux/authentication/actionCreator';

const rootUrl = '/';
const baseUrl = rootUrl + '/api';
let res = [];
let result = {
    result: null,
    error: true,
    message: null,
    errors: {},
    forceStop: false
};

Axios.interceptors.request.use(function (config) {
    const token = Cookies.get('token');
    config.headers.Authorization =  'Bearer ' + token;
    return config;
});

Axios.interceptors.response.use(
    response => {
        let status = response.data?.status ? response.data.status : false;
        let message = response.data?.message ? response.data.message : response.message;
        if(!message) message = 'Message Null';
        let errors = [];
        if(response.data?.errors) {
            Object.keys(response.data.errors).map(key => {
                let _data = response.data.errors[key];
                return errors.push({
                    name: key,
                    errors: _data
                })
            })
        }

        if(status === true) {
            result = {
                ...result,
                result: response.data,
                error: false,
                message: message,
                errors: errors,
                forceStop: false,
            };
        } else {
            result = {
                ...result,
                result: response.data,
                error: true,
                message: message,
                errors: errors,
                forceStop: false,
            };
        }

        return result;
},  error => {
        let response = error?.response;
        let message = response?.data?.message ? response.data.message : error?.message;
        let errors = [];
        if(response?.data?.errors) {
            Object.keys(response?.data.errors).map(key => {
                let _data = response.data.errors[key];
                return errors.push({
                    name: key,
                    errors: _data
                })
            })
        }

        if(response?.status === 401) {
            store.dispatch(loginModal(true));
            result = {
                ...result,
                result: response?.data,
                error: true,
                message: message,
                errors: errors,
                forceStop: true,
            };
        } else {
            result = {
                ...result,
                result: response?.data,
                error: true,
                message: message,
                errors: errors,
                forceStop: false,
            };
        }
        return Promise.reject(result);
});

const createParams = (params) => {
    const qs = Object.keys(params)
    .map(key => `${key}=` + (!params[key] ? '' : params[key]))
    .join('&');
    return qs;
}

export const api = (endpoint) => {
    return `${baseUrl}/${endpoint}`;
}

export const _get = async (url, data=null) => {
    await Axios.get(url, data)
    .then(response => res = response)
    .catch(error => res = error);
    return res;
}

export const _post = async (url, data) => {
    await Axios.post(url, data)
    .then(response => res = response)
    .catch(error => res = error);
    return res;
}

export const _put = async (url, data) => {
    await Axios.put(url, data)
    .then(response => res = response)
    .catch(error => res = error);
    return res;
}

export const _delete = async (url, data) => {
    await Axios.delete(url, data)
    .then(response => res = response)
    .catch(error => res = error);
    return res;
}

export const login = async (data) => {
    return await _post( api('auth/login'), data);
}

