import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom";
import { PageHeader, Row, Col, Image, Descriptions, Modal } from 'antd';
import { ContentSection } from '../styled';
import { Button } from '../../components/buttons/buttons';
import { Cards } from '../../components/cards/frame/cards-frame';

import { create_appointment } from '../../api';
import { registerStorage, registerStorageClear } from '../../utility/localStorageControl';
import { format_rupiah } from '../../utility/utility';

import {
    loadingStart,
    loadingClose,
    loadingContent,
    loadingSuccess,
    loadingError
} from '../../redux/loadingmodal/actionCreator';

class Confirmation extends React.Component {

    state = {
        schedule: {},
        register: {},
        person: {},
        visible: false
    }

    dispatch = this.props.dispatch;

    componentDidMount() {
        let storage = registerStorage();
        let schedule = JSON.parse(storage.scheduleObj);
        let person = JSON.parse(storage.personObj);
        
        this.setState({
            ...this.state,
            schedule: schedule,
            register: storage,
            person: person
        });
    }

    showModal = () => {
        this.setState({
          visible: true,
        });
      };
    
      hideModal = () => {
        this.setState({
          visible: false,
        });
      };

    confirm = async () => {
        this.setState({
            visible: false,
          });
        this.dispatch(loadingStart());
        this.dispatch(loadingContent('Melakukan pendaftaran...'))

        let params = {
            schedule_id: this.state.schedule.schedule_id,
            consul_date: this.state.register.consul_date,
            consul_time: this.state.register.consul_time,
            person_id: this.state.person.person_id,
            allergy: this.state.register.allergy,
            body_temperature: this.state.register.body_temperature,
            blood_pressure: this.state.register.blood_pressure,
            weight: this.state.register.weight,
            height: this.state.register.height,
            main_complaint: this.state.register.main_complaint,
            disease_history: this.state.register.disease_history,
        };

        const {result, error, message, errors, forceStop} = await create_appointment(params);
        
        if(forceStop) return;

        if(error) {
            this.dispatch(loadingError());
            this.dispatch(loadingContent(message))
            setTimeout(() => this.dispatch(loadingClose()), 5000);
        } else {
            this.dispatch(loadingSuccess());
            this.dispatch(loadingContent(message))
            
            setTimeout( () => {
                this.dispatch(loadingClose());
                registerStorageClear();
                return this.props?.history.push('/appointment_history/' + result.data.appointment.id);
            }, 3000);
        }
    }

    render() {
        return(
            <ContentSection>
                <PageHeader
                    ghost={false}
                    onBack={() => this.props?.history.push('/appointment/register/data')}
                    title="Langkah 4/4: Konfirmasi Pendaftaran"
                /> <br/> <br/>
                <Row gutter={8}>
                    <Col lg={12} xs={24}>
                        <Cards headless>
                            <figure className="text-center">
                                <Image
                                    width={200}
                                    preview={false}
                                    src={this.state?.schedule?.doctor_pic}
                                />
                            </figure>
                            <Descriptions title="Jadwal Telekonsultasi" column={1} bordered>
                                <Descriptions.Item label="Dokter">{this.state.schedule.doctor}</Descriptions.Item>
                                <Descriptions.Item label="Departemen">{this.state.schedule.department}</Descriptions.Item>
                                <Descriptions.Item label="Cabang">{this.state.schedule.branch}</Descriptions.Item>
                                <Descriptions.Item label="Tanggal">{this.state.schedule?.date?.find(row => row.date === this.state.register.consul_date).translate}</Descriptions.Item>
                                <Descriptions.Item label="Jam">{this.state.register.consul_time}</Descriptions.Item>
                                <Descriptions.Item label="Durasi">{this.state.schedule.duration} Menit</Descriptions.Item>
                                <Descriptions.Item label="Tarif">
                                    <span className="color-error">Rp {format_rupiah(this.state.schedule.fee)}</span>
                                </Descriptions.Item>
                            </Descriptions>
                        </Cards>
                    </Col>
                    
                    <Col lg={12} xs={24}>
                        <Cards headless>
                            <figure className="text-center">
                                <Image
                                    width={200}
                                    preview={false}
                                    src={this.state?.person?.profile_pic}
                                />
                            </figure>
                            <Descriptions title="Informasi Pasien" column={1} bordered>
                                <Descriptions.Item label="Nama">{this.state.person.full_name}</Descriptions.Item>
                                <Descriptions.Item label="Keluhan Utama">{this.state.register.main_complaint}</Descriptions.Item>
                                <Descriptions.Item label="Riwayat Penyakit">{this.state.register.disease_history ? this.state.register.disease_history : '-'}</Descriptions.Item>
                                <Descriptions.Item label="Alergi">{this.state.register.allergy ? this.state.register.allergy : '-'}</Descriptions.Item>
                                <Descriptions.Item label="Suhu Badan">{this.state.register.body_temperature ? this.state.register.body_temperature : '-'} ℃</Descriptions.Item>
                                <Descriptions.Item label="Tekanan Darah">{this.state.register.blood_pressure ? this.state.register.blood_pressure : '- '} mmHg</Descriptions.Item>
                                <Descriptions.Item label="Berat Badan">{this.state.register.weight ? this.state.register.weight : '-'} Kg</Descriptions.Item>
                                <Descriptions.Item label="Tinggi Badan">{this.state.register.height ? this.state.register.height : '-'} Cm</Descriptions.Item>
                            </Descriptions>
                        </Cards>
                    </Col>

                    <Col lg={24}>
                        <Button type="primary" size="large" onClick={this.showModal.bind(this)} block={true}>
                            Lakukan Pendaftaran
                        </Button>
                    </Col>
                </Row>
                <Modal
                    title="Persetujuan"
                    visible={this.state.visible}
                    onOk={this.confirm.bind(this)}
                    onCancel={this.hideModal.bind(this)}
                    okText="Ya, Saya Setuju"
                    cancelText="Batalkan Pendaftaran"
                    centered
                    width={1000}
                >
                    Semua isi dan bentuk konsultasi akan direkam dalam Rekam Medik pasien <br/>
                    <br/>
                    Nama pasien : {this.state.person.full_name} <br/>
                    Usia: {this.state.person.age} <br/>
                    Tgl lahir: {this.state.person.birth_date} <br/>
                    <br/>
                    Saya selaku pasien sendiri atau (ayah /ibu/anak/ saudara/i ……dari pasien) MENYETUJUI bahwa <br/>
                    <ol>
                        <li>Setuju untuk melakukan konsultasi dengan dokter via Whatsapp / Zoom, saya juga memahami bahwa dalam hal layanan ini tidak dilakukan pemeriksaan fisik dan kontak langsung dengan dokter.</li>
                        <li>Berjanji akan jujur dalam menuliskan / memberitahukan keluhan pasien, agar dokter dapat menentukan diagnosa dan penatalaksanaan yang tepat.</li>
                        <li>Tidak akan menuntut lebih lanjut jika ada hal terkait konsultasi via Whatsapp / Zoom ini.</li>
                    </ol>
                    Surat persetujuan ini berlaku sama dengan surat persetujuan manual pada umumnya.    
                </Modal>
            </ContentSection>
        );
    }
}

export default connect()(withRouter(Confirmation));