import React, { Suspense } from 'react';
import { withRouter, Link } from "react-router-dom";
import { PageHeader, Row, Col, Table, Skeleton, Avatar, Pagination } from 'antd';

import { Countdown } from '../../components/countdown';
import { TableWrapper, ContentSection } from '../styled';
import { get_appointment_history } from '../../api';
import { Cards } from '../../components/cards/frame/cards-frame';
import Heading from '../../components/heading/heading';
import { Button, BtnLabel } from '../../components/buttons/buttons';

import {
    label_apstatus
} from '../../utility/utility';

class AppointmentHistory extends React.Component {

    state = {
        list: [],
        filters: {
            paginate: true,
            data_per_page: 15,
            page: 1
        },
        tableLoading: true,
        source: [],
        data: {},
    }

    getData = async() => {
        
        const {result, error} = await get_appointment_history(this.state.filters);

        if(error) {
            console.log('error:', error);
            this.setState({
                ...this.state, tableLoading: false
            });
        } else {
            this.setState({
                ...this.state, list: result.data?.data, tableLoading: false, data: result.data
            },() => {
                this.processData();
            });
        }
    }

    componentDidMount() {
        this.getData();
    }

    processData = () => {
        let source = [];
        this.state.list.map(row => {
            return source.push({
                key: row.aid,
                id: row.aid,

                data: (
                    <Cards headless={true}>
                        <b>ID: </b> {row.aid} <br/>
                        <b>Pasien</b> <br/>
                        {row.patient_name} <br/>
                        <b>Dokter</b> <br/>
                        {row.doctor_name} <br/>
                        <b>Tanggal Konsul</b> <br/>
                        {row.id_consul_date} {row.consul_time} <br/>
                        <b>Status</b> <br/>
                        {label_apstatus(row.status)} <br/> <br/>

                        <Link to={`/appointment_history/${row.aid}`}>
                            <Button className="btn-icon" size="small" block={true} type="primary" title="Detail" onClick={() =>  this.props?.history.push(`/appointment_history/${row.aid}`) }>
                                <i aria-hidden="true" className="fa fa-search color-white"></i> Detail
                            </Button>
                        </Link>
                    </Cards>
                ),

                patient: (
                    <div className="user-info">
                        <figure>
                            <Suspense
                                fallback={
                                    <Skeleton avatar active/>
                                }
                            >
                                <Avatar size={{ xs: 40, sm: 40, md: 40, lg: 40, xl: 40, xxl: 40 }} src={row.patient_pic} />
                            </Suspense>
                        </figure>
                        <figcaption>
                            <Heading className="user-name" as="h6">
                            {row.patient_name}
                            </Heading>
                            <span className="user-designation">{row.age}</span>
                        </figcaption>
                    </div>
                ),
                doctor: (
                    <div className="user-info">
                        <figure>
                            <Suspense
                                fallback={
                                    <Skeleton avatar active/>
                                }
                            >
                                <Avatar size={{ xs: 40, sm: 40, md: 40, lg: 40, xl: 40, xxl: 40 }} src={row.doctor_pic} />
                            </Suspense>
                        </figure>
                        <figcaption>
                            <Heading className="user-name" as="h6">
                            {row.doctor_name}
                            </Heading>
                            <span className="color-primary">{row.department} | {row.branch}</span>
                        </figcaption>
                    </div>
                ),
                date: (
                    <div>
                        {row.consul_date} {row.consul_time} <br/>
                        
                        {   row.status === 'waiting_payment' ? <small className="color-error">Bayar Tagihan sebelum: <br/> {row.payment_expired_at}</small> :
                            row.status === 'payment_expire' ? '' : <Countdown date={row.consul_date} time={row.consul_time}/>
                        }
                    </div>
                ),
                status: label_apstatus(row.status),
                action: (
                        <>
                            <Link to={`/appointment_history/${row.aid}`}>
                                <Button className="btn-icon" size="default" shape="round" type="primary" title="Detail" onClick={() =>  this.props?.history.push(`/appointment_history/${row.aid}`) }>
                                    <i aria-hidden="true" className="fa fa-search color-white"></i>
                                </Button>
                            </Link>
                        </>
                )
            });
        });

        this.setState({
            ...this.state, source: source
        });
    }

    onTableChange = () => {

    }

    onPageChange = (e) => {
        this.setState({
            ...this.state,
            filters: {
                ...this.state.filters,
                page: e
            }
        }, () => {
            this.getData();
        });
    }

    render() {

        let {
            data = {},
            filters = {},
        } = this.state;

        const columns = [
            { title: 'Data', dataIndex: 'data', key: 'data', responsive: ['xs'] },
            { title: 'ID', dataIndex: 'id', key: 'id', responsive: ['sm'] },
            { title: 'Pasien', dataIndex: 'patient', key: 'patient', responsive: ['sm'] },
            { title: 'Dokter', dataIndex: 'doctor', key: 'doctor', responsive: ['sm'] },
            { title: 'Tanggal', dataIndex: 'date', key: 'date', responsive: ['sm'] },
            { title: 'Status', dataIndex: 'status', key: 'status', responsive: ['sm'] },
            { title: '#', dataIndex: 'action', key: 'action', width: '150px', responsive: ['sm'] },
        ];

        return(
            <ContentSection>
                <PageHeader
                    ghost={false}
                    onBack={() => window.history.back()}
                    title="Riwayat Pendaftaran"
                />
                <Row gutter={[25, 25]}>
                    <Col span={24}>
                        <Cards headless={true} >
                            <div className="table-responsive">
                                <TableWrapper>
                                    <Table
                                        loading={this.state.tableLoading}
                                        bordered={false}
                                        columns={columns}
                                        dataSource={this.state.source}
                                        onChange={this.onTableChange.bind(this)}
                                        pagination={false} 
                                    />
                                </TableWrapper>
                            </div>
                        </Cards>
                    </Col>
                    <Col span={24} className="text-right">
                        <Pagination
                            defaultCurrent={data?.current_page}
                            total={data?.total}
                            pageSize={filters?.data_per_page}
                            onChange={this.onPageChange.bind(this)}
                            showQuickJumper={true}
                            showTotal={total => `Total ${total} data`}
                        />
                    </Col>
                </Row>
            </ContentSection>
        );
    }
}

export default withRouter(AppointmentHistory);