import React from  'react';
import { Row, Col, Modal, Card } from 'antd';
import { Button } from '../../components/buttons/buttons';
import RegisterForm from '../../components/form/form/register';
import ResetPasswordForm from '../../components/form/form/resetPassword';
import Heading from '../../components/heading/heading';
import { useDispatch } from 'react-redux';
import { Cards } from '../../components/cards/frame/cards-frame';
import {
  loginModal, loginModalProps
} from '../../redux/authentication/actionCreator';
import { SupportContentWrap } from '../styled';
import { Link } from 'react-router-dom';

const Index = (props) => {

  const dispatch = useDispatch();
  const [ResetPasswordVisible, setResetPasswordVisible] = React.useState(false);
  const [modal, setModal] = React.useState({
    visible: false,
  });

  const showModal = () => {
    setModal({
      ...modal, visible: true
    });
  }

  const closeModal = () => {
    setModal({
      ...modal, visible: false
    });
  }

  const showLogin = () => {
    dispatch(loginModal(true));
    dispatch(loginModalProps({
      onCancel: () => dispatch(loginModal(false)),
      maskClosable: true,
      closeIcon: false
    }));
  }
  
  const showResetPassword = () => {
    setResetPasswordVisible(true);
  }

  return(<>
  <Row gutter={[25, 25]} style={{height: '90vh', marginLeft: '0px', marginRight: '0px'}}>
    <Col lg={{
      span: 12,
      offset: 6
    }}
    xs={24}
    className="text-center"
    style={{
      margin: 'auto'
    }}
    >
      <SupportContentWrap>
        <div className="sDash-support-container">
          <div className="sDash-support-links">
            <Row gutter={30}>
              <Col lg={12} xs={24}>
                <div className="sDash-support-link-item">
                  <div className="sDash-support-link-item__icon info">
                    <img src={require(`../../static/img/icon/enter.svg`).default} alt="" />
                  </div>
                  <h4 className="sDash-support-link-item__title">Masuk</h4>
                  <div className="sDash-support-link-item__content">
                    <p>Masuk ke aplikasi telekonsul</p>
                    <Button className="btn-link" onClick={showLogin}>
                      Login
                    </Button>
                  </div>
                </div>
              </Col>
              <Col lg={12} xs={24}>
                <div className="sDash-support-link-item">
                  <div className="sDash-support-link-item__icon success">
                    <img
                      src={require(`../../static/img/icon/test.svg`).default}
                      alt=""
                    />
                  </div>
                  <h4 className="sDash-support-link-item__title">Daftar</h4>
                  <div className="sDash-support-link-item__content">
                    <p>Daftar untuk mendapatkan akun</p>
                    <Button className="btn-link" onClick={showModal}>
                      Daftar
                    </Button>
                  </div>
                </div>
              </Col>
              <Col lg={12} xs={24}>
                <div className="sDash-support-link-item">
                  <div className="sDash-support-link-item__icon success">
                    <img
                      src={require(`../../static/img/icon/password.svg`).default}
                      alt=""
                    />
                  </div>
                  <h4 className="sDash-support-link-item__title">Reset Password</h4>
                  <div className="sDash-support-link-item__content">
                    <p>Ubah Password Akun Anda</p>
                    <Button className="btn-link" onClick={showResetPassword}>
                      Reset Password
                    </Button>
                  </div>
                </div>
              </Col>
              <Col lg={12} xs={24}>
                <div className="sDash-support-link-item">
                  <div className="sDash-support-link-item__icon primary">
                    <img src={require(`../../static/img/icon/landing-page.svg`).default} alt="" />
                  </div>
                  <h4 className="sDash-support-link-item__title">Landing Page</h4>
                  <div className="sDash-support-link-item__content">
                    <p>Kembali ke landing page</p>
                    <a href="/" className="btn-link color-dark">
                      Menuju Landing Page
                    </a>
                  </div>
                </div>
              </Col>
            </Row>
          </div>
        </div>
      </SupportContentWrap>
    </Col>
  </Row>
  <Modal
    key="ModalLogin"
    visible={modal.visible}
    onCancel={closeModal}
    noFooter={true}
    footer={null}
    >
      <Heading as="h3">
          Register Aplikasi <span className="color-primary">Telekonsultasi</span>
      </Heading>
      <span>Silahkan lengkapi form berikut</span> <br/> <br/>
      <RegisterForm/>
  </Modal>

  <Modal
    key="ModalResetPassword"
    visible={ResetPasswordVisible}
    onCancel={() => setResetPasswordVisible(false)}
    noFooter={true}
    footer={null}
    >
      <Heading as="h3">
          Reset Password Aplikasi <span className="color-primary">Telekonsultasi</span>
      </Heading>
      <span>Silahkan lengkapi form berikut</span> <br/> <br/>
      <ResetPasswordForm/>
  </Modal>
  </>);
}

export default Index;