import React from 'react';
import { Table, Row, Col, PageHeader, Pagination } from 'antd';
import { TableWrapper, ContentSection } from '../styled';
import { useHistory, Link } from 'react-router-dom';

// Component
import { Cards } from '../../components/cards/frame/cards-frame';
import { Button } from '../../components/buttons/buttons';
import { AlertError } from '../../components/alerts/alerts';

import { get_bills } from '../../api';
import { format_rupiah, payment_label } from '../../utility/utility';

const List = (props) => {

    const history = useHistory();
    const [loading, setLoading] = React.useState(true);
    const [alert, setAlert] = React.useState(null);
    const [data, setData] = React.useState({});
    const [source, setSource] = React.useState([]);
    const [filter, setFilter] = React.useState({
        paginate: true,
        data_per_page: 15
    });

    const getData = async () => {
        setLoading(true);
        setAlert(null);

        const {
            result, error, message
        } = await get_bills(filter);

        if(error) {
            setAlert(<AlertError message={message}/>);
        } else {
            setData(result.data);
        }

        setLoading(false);
    }

    React.useEffect(() => {
        getData();
    }, []);

    const processData = () => {
        console.log(`data`, data)
        let result = [];
        let _data = data?.data ? data.data : [];

        _data.map(row => {
            return result.push({
                key: row.bill_id,

                data: (<>
                    <Cards headless={true}>
                        <b>Nomor Tagihan</b> <br/>
                        {row.bill_id} <br/>
                        <b>Keterangan</b> <br/>
                        {row.description} <br/>
                        <b>Nominal</b> <br/>
                        {format_rupiah(row.amount, true)} <br/>
                        <b>Status</b> <br />
                        {payment_label(row.status)} <br /><br />

                        <Link to={`/bills/${row.bill_id}`}>
                            <Button className="btn-icon" size="default" shape="round" type="primary" title="Detail" onClick={() =>  history.push(`/bills/${row.bill_id}`) }>
                                <i aria-hidden="true" className="fa fa-search color-white"></i>
                            </Button>
                        </Link>
                    </Cards>
                </>),

                id: (`#${row.bill_id}`),
                description: row.description,
                amount: format_rupiah(row.amount, true),
                status: payment_label(row.status),
                action: (
                    <>
                        <Link to={`/bills/${row.bill_id}`}>
                            <Button className="btn-icon" size="default" shape="round" type="primary" title="Detail" onClick={() =>  history.push(`/bills/${row.bill_id}`) }>
                                <i aria-hidden="true" className="fa fa-search color-white"></i>
                            </Button>
                        </Link>
                    </>
                )
            });
        });
        
        setSource(result);
    }

    React.useEffect(processData, [data]);

    const onTableChange = (e) => {
        setFilter({
            ...filter,
            page: e.current,
            data_per_page: e.pageSize,
        });
    }

    const columns = [
        { title: 'Data', dataIndex: 'data', key: 'data', responsive: ['xs'] },
        { title: 'Nomor Tagihan', dataIndex: 'id', key: 'id', responsive: ['sm'] },
        { title: 'Berita', dataIndex: 'description', key: 'description', responsive: ['sm'] },
        { title: 'Nominal', dataIndex: 'amount', key: 'amount', responsive: ['sm'] },
        { title: 'Status', dataIndex: 'status', key: 'status', responsive: ['sm'] },
        { title: '#', dataIndex: 'action', key: 'action', width: '150px', responsive: ['sm'] },
    ];

    return(
        <ContentSection>
            <PageHeader
                ghost={false}
                onBack={() => window.history.back()}
                title="Daftar Tagihan"
            />
            {alert}
            <Row gutter={[25, 25]}>
                <Col span={24}>
                    <Cards headless={true} >
                        <div className="table-responsive">
                            <TableWrapper>
                                <Table
                                    loading={loading}
                                    bordered={false}
                                    columns={columns}
                                    dataSource={source}
                                    onChange={onTableChange}
                                    pagination={false} 
                                />
                            </TableWrapper>
                        </div>
                    </Cards>
                </Col>
                <Col span={24} className="text-right">
                    <Pagination
                        defaultCurrent={data?.current_page}
                        total={data?.total}
                        pageSize={filter?.data_per_page}
                        onChange={onTableChange}
                        showQuickJumper={true}
                        showTotal={total => `Total ${total} data`}
                    />
                </Col>
            </Row>
        </ContentSection>
    );
}

export default List;