import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { PageHeader, Form, Row, Col, Input} from 'antd';

import { ContentSection } from '../styled';
import { Button } from '../../components/buttons/buttons';
import Heading from '../../components/heading/heading';
import { AlertError, AlertSuccess } from '../../components/alerts/alerts';

import { update_account, update_password } from '../../api';

import {
    loadingStart,
    loadingClose,
    loadingContent,
} from '../../redux/loadingmodal/actionCreator';

const Profile = (props) => {
    const dispatch = useDispatch();

    const [form] = Form.useForm();
    const [formPass] = Form.useForm();

    const [account, setAccount] = useState({});
    const [alertAccount, setAlertAccount] = useState(null);
    const [alertPassword, setAlertPassword] = useState(null);

    useEffect(() => {
        let log = JSON.parse(localStorage.getItem('personData'));
        setAccount(log);

        form.setFieldsValue({
            email: log?.email,
            phone_number: log?.phone_number,
        });

    }, []);

    const changeAccount = async (fields) => {

        setAlertAccount(null);
        dispatch(loadingStart());
        dispatch(loadingContent('Memproses permintaan...'))

        const {
            result, error, message, errors, forceStop
        } = await update_account(fields);
        if(forceStop) return;

        dispatch(loadingClose());

        if(error) {
            setAlertAccount(
                <AlertError message={message} />
            );
            form.setFields(errors);
            return;
        }

        setAlertAccount(<AlertSuccess message={message} />);

        let log = {
            ...account,
            email: result.data.email,
            phone_number: result.data.phone_number
        };

        setAccount(log);
        localStorage.setItem('personData', JSON.stringify(log))

    }

    const changePassword = async (fields) => {

        if(fields.new_password !== fields.confirm_password) {
            formPass.setFields([
                {
                    name: 'confirm_password',
                    errors: [
                        'Konfirmasi password tidak sesuai dengan password baru'
                    ]
                }
            ]);
            return;
        }

        setAlertPassword(null);
        dispatch(loadingStart());
        dispatch(loadingContent('Memproses permintaan...'));

        const {
            error, message, errors, forceStop
        } = await update_password(fields);
        if(forceStop) return;

        dispatch(loadingClose());

        if(error) {
            setAlertPassword( <AlertError message={message} /> );
            formPass.setFields(errors);
            return;
        }

        setAlertPassword(<AlertSuccess message={message} />);
    }

    return(
        <ContentSection>
            <PageHeader
                ghost={false}
                onBack={() => window.history.back()}
                title="Pengaturan Akun"
            />
            <Row gutter={8}>
                <Col
                    lg={{
                        span: 12,
                        offset: 6
                    }}
                    xs={24}
                >
                    <Heading as="h3" className="text-center">Ubah Akun Login</Heading>
                    {alertAccount}
                    <Form
                        form={form}
                        layout="vertical"
                        onFinish={changeAccount}
                        scrollToFirstError={true}
                    >
                        <Form.Item
                            label="Alamat Email"
                            rules={[
                                { required: true, message: "Masukan alamat email" },
                                { type: 'email', message: 'Alamat email tidak valid' }
                            ]}
                            name="email"
                        >
                            <Input type="email" placeholder="..." />
                        </Form.Item> <br/>

                        <Form.Item
                            label="Nomor Telepon"
                            rules={[
                                { required: true, message: "Masukan nomor telepon" }
                            ]}
                            name="phone_number"
                        >
                            <Input placeholder="..." />
                        </Form.Item> <br/>

                        <Form.Item>
                            <Button type="primary" size="large" htmlType="submit" className="login-form-button" block={true}>
                                Ubah Akun
                            </Button>
                        </Form.Item>
                    </Form>  <br/> <br/>
                </Col>

                <Col
                    lg={{
                        span: 12,
                        offset: 6
                    }}
                    xs={24}
                >
                    <Heading as="h3" className="text-center">Ubah Password</Heading>
                    {alertPassword}
                    <Form
                        form={formPass}
                        layout="vertical"
                        onFinish={changePassword}
                        scrollToFirstError={true}
                    >
                        <Form.Item
                            label="Password saat ini"
                            rules={[
                                { required: true, message: "Masukan password saat ini" }
                            ]}
                            name="current_password"
                        >
                            <Input.Password placeholder="..." autoComplete="false"/>
                        </Form.Item> <br/>

                        <Form.Item
                            label="Password Baru"
                            rules={[
                                { required: true, message: "Masukan masuk password" }
                            ]}
                            name="new_password"
                        >
                            <Input.Password placeholder="..." autoComplete="false"/>
                        </Form.Item> <br/>


                        <Form.Item
                            label="Konfirmasi Password Baru"
                            rules={[
                                { required: true, message: "Masukan konfirmasi masuk password" }
                            ]}
                            name="confirm_password"
                        >
                            <Input.Password placeholder="..." autoComplete="false"/>
                        </Form.Item> <br/>

                        <Form.Item>
                            <Button type="primary" size="large" htmlType="submit" className="login-form-button" block={true}>
                                Ubah Password
                            </Button>
                        </Form.Item>
                    </Form>
                </Col>
            </Row>
        </ContentSection>
    );
}

export default Profile;