import React from 'react';
import Swal from 'sweetalert2'
import { PageHeader, Popover, Card, Row, Col, Modal, Avatar, Table, Form, Input, Skeleton, Empty, Pagination } from 'antd';
import { Link } from 'react-router-dom';

import { ContentSection, CardBorder, FilterStyle, PopoverFormWrapper, PopoverButtonGroup } from '../styled';
import { Button } from '../../components/buttons/buttons';
import { SelectWeekday, SelectBranch, SelectDepartment } from '../../components/form/select';
import { day_tanslate } from '../../utility/utility';

import { get_doctor_schedule } from '../../api';

class Schedule extends React.Component {

    state = {
        data: {},
        modalVisible: false,
        doctor: {},
        filters: {
            data_per_page: 25,
            paginate: true,
            query: null,
            department_id: null,
            branch_id: null,
            doctor_name: null
        },
        loading: true,
    };
    form = React.createRef();

    componentDidMount() {
        this.getData();
    }


    getData = async () => {
        this.setState({ ...this.state, loading: true });
        const {result, error} = await get_doctor_schedule(this.state.filters);
        this.setState({ ...this.state, loading: false });

        if(error) {
            console.log('error:', error);
        } else {
            this.setState({
                ...this.state, data: result.data
            });
        }
    }

    onPageChange = (e) => {
        this.setState({
            ...this.state,
            filters: {
                ...this.state.filters,
                page: e
            }
        }, () => {
            this.getData();
        });
    }

    showSchedule = (index) => {
        if(typeof this.state.data?.data[index] === 'undefined') {
            Swal.fire({
                icon: 'error',
                title: 'Terjadi kesalahan...',
                text: 'Dokter tidak ditemukan, silahkan refresh halaman ini.',
            });
            return;
        }
        this.setState({
            ...this.state,
            doctor: this.state.data?.data[index],
            modalVisible: true,
        })
    }

    filterOption = (e) => {
        console.log(e);
    }

    applyFilter = (e) => {
        this.setState({
            ...this.state,
            filters: {
                ...this.state.filters,
                ...e
            }
        },() => {
            this.getData();
        });
    }

    resetFormFilters = () => {
        this.setState({
            ...this.state,
            filters: {}
        },() => {
            // this.form.current.resetFields();
            this.form.current.submit();
        })
    }

    render() {

        const {
            data, modalVisible, doctor, filters
        } = this.state;

        const FormFilter = () => {

            let {
                branch_id = null,
                department_id = null,
                doctor_name = null,
                days = [],
            } = this.state.filters;

            return (
                <PopoverFormWrapper>
                    <Form
                        ref={this.form}
                        onFinish={this.applyFilter.bind(this)}
                        layout="vertical"
                        size="small"
                    >
                        <Row gutter={8}>
                            <Col lg={12} xs={24}>
                                <Form.Item
                                    label="Cabang"
                                    name="branch_id"
                                    initialValue={branch_id}
                                >
                                    <SelectBranch searchable="true" dropdownClassName="ant-select-popover" />
                                </Form.Item>
                            </Col>

                            <Col lg={12} xs={24}>
                                <Form.Item
                                    label="Poli"
                                    name="department_id"
                                    initialValue={department_id}
                                >
                                    <SelectDepartment searchable="true" dropdownClassName="ant-select-popover" />
                                </Form.Item>
                            </Col>
                        </Row>

                        <Row gutter={8}>
                            <Col lg={12} xs={24}>
                                <Form.Item
                                    label="Dokter"
                                    name="doctor_name"
                                    initialValue={doctor_name}
                                >
                                    <Input placeholder="..."/>
                                </Form.Item>
                            </Col>

                            <Col lg={24} xs={24}>
                                <Form.Item
                                    label="Hari"
                                    name="days"
                                    initialValue={days}
                                >
                                    
                                    <SelectWeekday mode="multiple" searchable="true" dropdownClassName="ant-select-popover" />
                                </Form.Item>
                            </Col>
                        </Row>
                        
                        <Form.Item style={{display:'none'}}>
                            <Button type="primary" htmlType="submit">
                            Submit
                            </Button>
                        </Form.Item>
                    </Form>
                    <PopoverButtonGroup>
                            <Button size="small" type="danger" outlined
                            onClick={() => this.resetFormFilters()}
                            >
                                Bersihkan Pencarian
                            </Button>
                            <Button size="small" type="primary"
                            onClick={() => this.form.current.submit()}
                            >
                                Terapkan Pencarian
                            </Button>
                        </PopoverButtonGroup>
            </PopoverFormWrapper>
            );
        }

        const ExtraHeader = () => {
            return (
                <FilterStyle>
                    <Popover placement="bottomLeft" title="Filter Pencarian" content={FormFilter} action="click">
                        <Button type="danger" className="active">
                            Filter
                        </Button>
                    </Popover>
                </FilterStyle>
            );
        }

        const ScheduleModal = () => {
            const  columns = [
                { title: 'Hari', key: 'days', dataIndex: 'days' },
                {
                    title: 'Jadwal', key: 'schedule', dataIndex: 'schedule',
                    render: row  => {
                        let data = Object.keys(row).map(index => {
                                let time = row[index];
                                return <span key={time.schedule_id}><Link to={"/appointment/register/schedule?schedule_id=" + time.schedule_id} title="Lanjut ke Pendaftaran"> {time.start_hour} - {time.end_hour} </Link> { (index+1) >= row.length ? '' : '|' } </span>;
                        });
                        return data;
                    }
                },
            ];
    
            const source = [];
    
            const schedule = doctor?.schedule ? doctor.schedule : {};
    
            Object.keys(schedule).map(day => {
                let row = schedule[day];
                if( row.length > 0 ) {
                    source.push({
                        key: day,
                        days: day_tanslate(day),
                        schedule: row
                    })
                }
                return row;
            });
    
            return(<Modal
                visible={modalVisible}
                footer={null}
                centered={true}
                onCancel={() => this.setState({...this.state, modalVisible: false})}
            >
                <center>
                    <Avatar shape="square" size={256} src={doctor.profile_pic} /> <br/> <br/>
                    <strong>{doctor.name}</strong> <br/>
                    <strong className="color-primary">{doctor.department}</strong> <br/>
                    <small>{doctor.branch}</small> <br/> <br/>
                </center> <br/>
                <Table
                    size="small"
                    bordered
                    locale={{
                        emptyText: 'Tidak ada jadwal'
                    }}
                    columns={columns}
                    dataSource={source}
                    pagination={false}
                />
            </Modal>);
        }

        return (
            <ContentSection>
                <ScheduleModal/>
                <PageHeader
                    ghost={false}
                    onBack={() => window.history.back()}
                    title="Langkah 1/4: Pilih Jadwal Dokter"
                    extra={<ExtraHeader/>}
                /> <br/> <br/>
                <center>
                    <CardBorder>
                        <Row gutter={25}>
                            { this.state.loading && (
                                <Col lg={6} xs={24}>
                                    <Card>
                                        <Skeleton.Image />
                                        <Skeleton/>
                                    </Card>
                                </Col>
                            ) }
                            { (data?.data ? data.data : []).length === 0 && <Empty/> }
                            {   Object.keys(data?.data ? data.data : []).map(key => {
                                    let row = data?.data[key];
                                    return(<Col lg={6} sm={24} xs={24} key={key}>
                                        <Card
                                            hoverable
                                            style={{ width: 240 }}
                                            cover={<img alt="example" src={row.profile_pic} />}
                                            className="card-border"
                                        >
                                            <Card.Meta title={row.name} description={<>
                                                <strong className="color-primary">{row.department}</strong> <br/>
                                                <small>{row.branch}</small> <br/> <br/>
                                                <Button type="primary" onClick={() => this.showSchedule(key)}>
                                                    <i className="fa fa-calendar-plus-o" aria-hidden={true}></i>
                                                    Jadwal Praktek
                                                </Button> <br/> <br/>
                                            </>}/>
                                        </Card> <br/>
                                    </Col>);
                                })
                            }
                            <Col span={24} className="text-right">
                                <Pagination
                                    defaultCurrent={data?.current_page}
                                    total={data?.total}
                                    pageSize={filters?.data_per_page}
                                    onChange={this.onPageChange.bind(this)}
                                    showQuickJumper={true}
                                    showTotal={total => `Total ${total} data`}
                                />
                            </Col>
                        </Row>
                    </CardBorder>
                </center>
            </ContentSection>
        );
    }
}

export default Schedule;